<template>
    <div id="expertLibraryList">
         <!-- 搜索 -->
        <div class="queryForm">
            <el-form ref="queryForm" :model="queryForm" >
                <el-form-item label="姓名：" >
                    <el-input v-model="queryForm.name"></el-input>
                </el-form-item>
                <el-form-item label="工作性质：">
                    <el-select v-model="queryForm.work" clearable placeholder="请选择">
                        <el-option
                        v-for="item in queryForm.workOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="专业领域：">
                    <el-select v-model="queryForm.field" clearable placeholder="请选择">
                        <el-option
                        v-for="item in queryForm.fieldOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="省内专家/省外专家：" >
                    <el-select v-model="queryForm.expert" clearable placeholder="请选择">
                        <el-option
                        v-for="item in queryForm.expertOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                    <!-- <el-input v-model="queryForm.expert"></el-input> -->
                </el-form-item>
            </el-form>
            <el-button type="primary" @click="handleQuery">搜索</el-button>
        </div>
        <!-- 审核 -->
        <el-button type="primary" v-show="isShowEximeBtn" @click="handleExime">审核</el-button>
        <!-- 展示列表 -->
        <div class="showList">
            <!-- 表格 -->
            <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                v-loading="loading"
                @selection-change="handleSelectionChange">
                <el-table-column
                type="selection"
                align="center">
                </el-table-column>
                <el-table-column
                type="index"
                label="序号"
                align="center">
                </el-table-column>
                <el-table-column
                prop="realName"
                label="姓名"
                :show-overflow-tooltip="true"
                align="center">
                 <template slot-scope="scope">
                    <router-link :to="{
                        name:'ExpertLibraryShow',
                        params:{
                            id:scope.row.uid,
                            arrays:tableData
                        }
                        }"> 
                        {{scope.row.realName}}
                    </router-link>
                </template>
                </el-table-column>
                <el-table-column
                prop="expertInformation.natureWork"
                label="工作性质"
                align="center">
                    <template slot-scope="scope">
                        <span v-show="scope.row.expertInformation.natureWork == 30">科研(教育)</span>
                        <span v-show="scope.row.expertInformation.natureWork == 31">咨询(设计、规划)</span>
                        <span v-show="scope.row.expertInformation.natureWork == 32">管理及其他</span>
                    </template>
                </el-table-column>
                <el-table-column
                prop="expertInformation.professionalField"
                min-width="135"
                label="专业领域"
                align="center">
                </el-table-column>
                <el-table-column
                prop="expertInformation.education"
                label="学历"
                align="center">
                </el-table-column>
                <el-table-column
                prop="expertInformation.phone"
                label="手机"
                align="center">
                </el-table-column>
                <el-table-column
                prop="expertInformation.isProvince"
                label="省内专家/省外专家"
                align="center">
                </el-table-column>
                <el-table-column
                label="操作"
                align="center">
                    <template>
                        <el-button v-show="changeState==false" type="primary" size="small" @click="handleChangeState">停用</el-button>
                        <el-button v-show="changeState==true" type="primary" size="small" @click="handleChangeState">启用</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange"
                @handleTableFreshNum="handleTableFresh"
            ></pages>
        </div>
    </div>
</template>

<script>
export default {
    name:'expertLibraryList',
    data(){
        return{
            queryForm:{
                name:'',
                workOptions:[{
                    value:'0',
                    label:'科研（教育）'
                },{
                    value:'1',
                    label:'咨询（设计、规划）'
                },{
                    value:'2',
                    label:'管理'
                },{
                    value:'3',
                    label:'其他'
                }],
                work:'',
                fieldOptions:[{
                    value:'0',
                    label:'水环境治理'
                },{
                    value:'1',
                    label:'大气环境保护'
                },{
                    value:'2',
                    label:'土壤环境治理'
                },{
                    value:'3',
                    label:'固体废物处置'
                }],
                field:'',
                expertOptions:[{
                    value:'0',
                    label:'省内'
                },{
                    value:'1',
                    label:'省外'
                }],
                expert:''
            },
            tableData:[{
                radministratorInformation:'',
                expertInformation:{
                    natureWork:'',
                },
                identity:'',
                isDelete:'',
                isFirst:'',
                isState:'',
                loginName:'',
                password:'',
                realName:'',
                subaccount:'',
                uid:'',
            }],
            fenye:{
                total:0, //共有数据多少条
                pageNum:1,
                pageSize:10, //每页显示的条数
                pageSizes:[10,20,30,40] //选择每页显示多少条
            },
            loading:true,
            multipleSelection:[],
            selectedIDs:[],//储存选中每条信息的id
            selectedIsState:[],//存放每条信息的id
            changeState:true,
            isShowEximeBtn:true,//是否显示审核按钮，默认是显示的
            // isHeideExime:false,//隐藏审核按钮之后，showlist添加样式
            type:''
        }
    },
    methods:{
        handleSelectionChange:function(val){
            this.multipleSelection = val;
            var ids = []//id
            var isState = [] //存放每条信息的审核状态
            this.multipleSelection.map((item)=> {
                ids.push(item.uid)
                isState.push(item.isState)
            })
            this.selectedIDs = ids
            this.selectedIsState = isState
            this.isShowExime(this.selectedIsState)
        },
        // 是否显示审核按钮
        isShowExime(obj){
            if(obj[obj.length-1] == '1'){ //审核通过，则按钮不显示
                this.isShowEximeBtn = false
            }else{//2 表示审核没通过，等待审核
                this.isShowEximeBtn = true
            }
        },
        // 审核
        handleExime(){
            if(this.selectedIDs.length != 1){
                this.$alert('请选择一条数据进行审核', '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {
                }).catch(() => {
                })
            }else{
                this.$router.push({
                    name:'ExpertLibraryShow',
                    params:{
                        id:this.selectedIDs[0],
                        arrays:this.tableData,
                        isShow:1,
                    }
                })
            }
        },
        // 搜索
        handleQuery(){
            this.getTableData(this.queryForm.name,this.queryForm.work,this.queryForm.field,this.queryForm.expert,this.fenye.pageNum,this.fenye.pageSize)
        },
        // 分页
        handleTableFresh(){
            document.querySelector(".first-pager").click()
            this.getTableData(this.queryForm.name,this.queryForm.work,this.queryForm.field,this.queryForm.expert,this.fenye.pageNum,this.fenye.pageSize)
        },
        handleCurrentChange:function(val){
            this.fenye.pageNum = val
            this.getTableData(this.queryForm.name,this.queryForm.work,this.queryForm.field,this.queryForm.expert,this.fenye.pageNum,this.fenye.pageSize)
        },
        handleSizeChange:function(val){
            this.fenye.pageSize = val
            this.getTableData(this.queryForm.name,this.queryForm.work,this.queryForm.field,this.queryForm.expert,this.fenye.pageNum,this.fenye.pageSize)
        },
        // 点击停用/启用
        handleChangeState(){
            this.getChangeState()
        },
        getTableData(name,natureWork,professionalField,isProvince,Page,total){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expert/query',
                params:{
                    name,
                    natureWork,
                    professionalField,
                    isProvince,
                    Page,
                    total,
                },
                headers:{
                    'Content-type':'application/x-www-form-urlencoded'
                }
            }).then(function(res){
                if(res.data.data == null){
                    _this.tableData=[]
                    _this.loading = false
                }else{
                    _this.loading = false
                    _this.fenye.total = res.data.data.alltotal
                    _this.tableData = res.data.data.data
                }
                console.log(_this.tableData)
            }).catch(function(err){
                console.log(err)
            })
        },
        getChangeState(id,type){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expert/changeState',
                params:{
                    id,
                    type
                }
            }).then(function(res){
                console.log(res)
            }).catch(function(err){
                console.log(err)
            })
        }
    },
    created(){
        this.getTableData(this.queryForm.name,this.queryForm.work,this.queryForm.field,this.queryForm.expert,this.fenye.pageNum,this.fenye.pageSize)
    },
    // beforeMount(){
    //     this.getTableData(this.queryForm.name,this.queryForm.work,this.queryForm.field,this.queryForm.expert,this.fenye.pageNum,this.fenye.pageSize)
    // }
}
</script>
<style lang="less">
#expertLibraryList{
    .queryForm .el-form .el-form-item .el-form-item__label{
        width: 160px;
    }
    .showList{
        min-height: 630px;
        .el-table{
            min-height: 580px;
            padding-bottom: 10px;
        }
        .el-button{
            margin: 0;
        }
    }
    .pages{
        .el-button{
            background-color: transparent;
            border: 0;
            margin: 0;
            span{
                line-height: 1;
            }
        }
    }
}
</style>



