<template>
    <div id="resultsAddShow">
        <div class="addShow">
            <el-form :model="addForm" :ref="addForm">
                <div class="tableBox">
                    <!-- <el-button type="primary" @click="handleAddRow(1)">添加</el-button> -->
                    <span class="addTable"><i class="el-icon-circle-plus-outline" @click="handleAddRow(1)"></i></span>
                    <table class="table1" cellspacing=0 cellpadding=0>
                        <thead>
                            <tr>
                                <th colspan="4">一、专利一览表</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>序号</td>
                                <td>名称</td>
                                <td>专利申请时间</td>
                                <td>专利申请号</td>
                            </tr>
                            <tr v-for="(item,i) in tableNum">
                                <td>
                                    <el-input v-model="tableNum[i].serialNumber"></el-input>
                                </td>
                                <td>
                                    <el-input v-model="tableNum[i].outcomeName"></el-input>
                                </td>
                                <td>
                                    <el-date-picker
                                        v-model="tableNum[i].patentApplicationTime"
                                        align="right"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </td>
                                <td style="border-right:1px solid #e0e0e0;">
                                    <el-input v-model="tableNum[i].patentApplicationNumber"></el-input>
                                </td>
                                <span class="removeTable"><i class="el-icon-remove-outline" @click="handleRemoveRow(1,i)"></i></span>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="info">
                    <p>注：</p>
                    <p>1.请按表格顺序附专利申请受理通知书、专利证书等相关证明材料复印件，计算机软件需提供软件著作权登记证书；</p>
                    <p>2.专利申请时间应在课题立项时间之后，课题立项前申请的不得收入验收材料合订本。</p>
                </div>
                <div class="tableBox">
                    <!-- <el-button type="primary" @click="handleAddRow(2)">添加</el-button> -->
                    <span class="addTable"><i class="el-icon-circle-plus-outline" @click="handleAddRow(2)"></i></span>
                    <table class="table2" cellspacing=0 cellpadding=0>
                        <thead>
                            <tr>
                                <th colspan="5">二、论文、专著一览表</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>序号</td>
                                <td>名称</td>
                                <td>刊物</td>
                                <td>发表时间</td>
                                <td>作者</td>
                                <td>论文级别（SCI/EI/核心等）</td>
                            </tr>
                            <tr v-for="(item,i) in tableNum2"> 
                                <td>
                                    <el-input v-model="tableNum2[i].serialNumber"></el-input>
                                </td>
                                <td>
                                    <el-input v-model="tableNum2[i].name"></el-input>
                                </td>
                                <td>
                                    <el-input v-model="tableNum2[i].publication"></el-input>
                                </td>
                                <td>
                                    <el-date-picker
                                        v-model="tableNum2[i].publicationTime"
                                        align="right"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </td>
                                <td>
                                    <el-input v-model="tableNum2[i].author"></el-input>
                                </td>
                                <td style="border-right:1px solid #e0e0e0;">
                                    <el-input v-model="tableNum2[i].paperLevel"></el-input>
                                </td>
                                <span class="removeTable"><i class="el-icon-remove-outline" @click="handleRemoveRow(2,i)"></i></span>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="info">
                    <p>注：</p>
                    <p>1.请按表格顺序附论文正文、用稿证明或受理函等证明论文论著已发表；</p>
                    <p>2.发表的论文、论著等成果均应标注“江苏省环保科研课题”及课题编号，未标注的不得收入验收材料合订本。</p>
                </div>
                <ul>
                    <li>其它材料</li>
                    <li>项目新装备、新工艺、新产品、新材料等如合同中有要求提供，需提供实物图片、相关数据资料、有资质机构出具的有关产品测试报告、检测报告以及用户报告等（建议列表汇总，并附相关证明材料）；技术标准等其他形式及其相关证明材料。
                        附：课题成果用户使用报告</li>
                </ul>
                <table class="table3" cellspacing=0 cellpadding=0>
                    <thead>
                        <tr>
                            <th colspan="4">项目成果用户使用报告</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>课题编号：</td>
                            <td>
                                <el-input disabled v-model="addForm.topicNumber"></el-input>
                            </td>
                            <td>课题名称：</td>
                            <td><el-input disabled v-model="addForm.topicName"></el-input></td>
                        </tr>
                        <tr>
                            <td>应用单位名称：</td>
                            <td colspan="3">
                                <el-input disabled v-model="addForm.subjectUndertakingUnit"></el-input>
                            </td>
                            <!-- <td>通讯邮编：</td>
                            <td><el-input v-model="addForm.code"></el-input></td> -->
                        </tr>
                        <tr>
                            <td>通讯地址：</td>
                            <td colspan="3">
                                <el-input disabled v-model="addForm.postalAddress"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>成果应用开始时间：</td>
                            <td>
                                <el-date-picker
                                    v-model="addForm1.startDate"
                                    align="right"
                                    type="date"
                                    value-format="yyyy-MM-dd"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </td>
                            <td>成果应用结束时间：</td>
                            <td>
                                <el-date-picker
                                    v-model="addForm1.endDate"
                                    align="right"
                                    type="date"
                                    value-format="yyyy-MM-dd"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </td>
                        </tr>
                        <tr>
                            <td>应用情况、社会经济效益（含计算过程）：</td>
                            <td colspan="3">
                                <el-input type="textarea" v-model="addForm1.process"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>上传附件：</td>
                            <td colspan="3" style="height:50px;">
                                <input type="file" id="file">
                            </td>
                        </tr>
                    </tbody>
                </table>
            </el-form>
            <div class="button">
                <el-button type="primary" @click="handleConfirm">提交</el-button>
                <el-button type="primary" @click="handleSave">保存</el-button>
                <el-button type="primary" @click="handleBack">返回</el-button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'resultsAddShow',
    data(){
        return{
            addForm:{
                id: 62,
                postalAddress: "",
                subjectUndertakingUnit: "",
                topicName: "",
                topicNumber:'',
            },
            addForm1:{
                startDate:'',
                endDate:'',
                process:'',
            },
            tableNum:[{
                serialNumber:'',
                outcomeName:'',
                patentApplicationTime:'',
                patentApplicationNumber:''
            },{
                serialNumber:'',
                outcomeName:'',
                patentApplicationTime:'',
                patentApplicationNumber:''
            }],
            tableNum2:[{
                serialNumber:'',
                name:'',
                publication:'',
                publicationTime:'',
                author:'',
                paperLevel:''
            },{
                serialNumber:'',
                name:'',
                publication:'',
                publicationTime:'',
                author:'',
                paperLevel:''
            }],
            params:{
                id:this.$route.params.id,
                arrays:this.$route.params.arrays
            }
        }
    },
    methods:{
        handleAddRow(obj){
            if(obj == 1){
                let $obj = {
                    index:'',
                    name:'',
                    date1:'',
                    number:''
                }
                this.tableNum.push($obj)
            }else{
                let $obj = {
                    index:'',
                    publication:'',
                    date:'',
                    author:'',
                    sci:''
                }
                this.tableNum2.push($obj)
            }
        },
        handleRemoveRow(obj,index){
            if(obj == 1){
                this.tableNum.splice(index,1)
            }else{
                this.tableNum2.splice(index,1)
            }
        },
        // 主要学术成就的非空判断
        emptyJudgment(json){
            let arr =[]
            let $json = []
            for(let i=0;i<json.length;i++){
                $.each(json[i],function(index,val){
                    if(val != ''){
                        arr.push(i)
                        return false
                    }
                })
            }
            for(let j=0;j<arr.length;j++){
                $json.push(json[arr[j]]);
            }
            return $json
        },
        info($json){
            if($json.length > 0){
                for(let i in $json){
                    for(let j in $json[i]){
                        if($json[i][j] == '' || $json[i][j] == undefined){
                            return 'str'
                        }
                    }
                }
                return $json
            }else{
                return $json
            }
        },
        handleConfirm(){
            var $obj =  this.info(this.emptyJudgment(this.tableNum))//专利
            var $obj2 =  this.info(this.emptyJudgment(this.tableNum2))//论文
            console.log($obj)
            if($obj == 'str' || $obj2 == 'str'){
                this.$alert('请将主要学术成就部分补填完整', '提示', {
                    confirmButtonText: '确定',
                    callback:action => {}
                });
                return false
            }
            for(let i in this.addForm){
                if(typeof(this.addForm[i]) == "string" || typeof(this.addForm[i])== "Int"){
                    if(this.addForm[i].match(/^[ ]*$/) || this.addForm[i] == '' || this.addForm[i] == undefined){
                        this.$alert('请将表格填写完整', '提示', {
                            confirmButtonText: '确定',
                            type: 'warning',
                            callback: action => {}
                        });
                        return false;
                    }
                }
            }
            if(document.getElementById("file").files[0]==undefined){
                this.$alert('上传附件', '提示', {
                    confirmButtonText: '确定',
                    type: 'warning',
                    callback: action => {}
                });
                return false;
            }
            this.submitAxios($obj,$obj2)
        },
        submitAxios($obj,$obj2){
            let _this = this
            var formData = new FormData()
            let cid = _this.params.id//'验收申请表的id  (拼接在请求地址后面)'
            formData.append("cid",cid)
            var files = document.getElementById('file').files[0]
            formData.append("achievementFileUrl",files)
            let outcomeInformationAll=JSON.stringify({
                    topicNumber:_this.addForm.topicNumber,
                    topicName:_this.addForm.topicName,
                    applicationUnitName:_this.addForm.subjectUndertakingUnit,
                    postalAddress:_this.addForm.postalAddress,
                    achievementStartTime:_this.addForm1.startDate,
                    achievementEndTime:_this.addForm1.endDate,
                    economicPerformance:_this.addForm1.process,
                    checkApplyId:_this.params.id,//'验收申请表的id'
                    outcomeInformationPaperList:$obj2,
                    outcomeInformationPatentList:$obj,
                })
            console.log(outcomeInformationAll)
            formData.append("outcomeInformationAll",new Blob([outcomeInformationAll], {type: "application/json"}))
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/achievement/AddAchievement',
                data:formData
            }).then(function(res){
                console.log(res)
                let data = ''
                if(res.data.resultFlag == 0){//成功
                   data = res.data.data
                }
                if(res.data.resultFlag == 1){
                   data = res.data.data
                }
                _this.$alert(data, '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {
                    _this.$router.back(0)
                }).catch(() => {
                    _this.$router.back(0)
                })
            }).catch(function(err){
                console.log(err)
            })
        },
        handleSave(){

        },
        handleBack(){
            this.$router.back(-1)
        },
        getAddForm(formData){
            if(formData != undefined){
                formData.map((item) => {
                    if(this.params.id == item.id){
                        this.addForm = item
                        return;
                    }
                })
            }
        }
    },
    mounted(){
        console.log(this.params.arrays)
        this.getAddForm(this.params.arrays)
    }
}
</script>
<style lang="less">
#resultsAddShow{
    .addShow{
        width: 100%;
        min-height: 800px;
        background-color: #fff;
        padding-top: 14px;
        .el-form{
            width: 59%;
            margin: auto;
            min-width: 900px;
            .tableBox{
                position: relative;
                .el-button{
                    position: absolute;
                    top: 50px;
                    right: -82px;
                }
                .addTable{
                    position: absolute;
                    top: 70px;
                    right: -52px;
                    cursor: pointer;
                    .el-icon-circle-plus-outline:before{
                        font-size: 30px;
                    }
                }
                
            }
            table{
                tr{
                    position: relative;
                    &:last-child{
                        td{
                            border-bottom:  1px solid #e0e0e0;
                        }
                    }
                    td{
                        border-top: 1px solid #e0e0e0;
                        border-left: 1px solid #e0e0e0;
                        &:last-child{
                            border-right:  1px solid #e0e0e0;
                        }
                        .el-input__inner{
                            height: 50px;
                            line-height: 50px;
                            border: 0px;
                        }
                        .el-textarea__inner{
                            min-height: 190px !important;
                            border: 0px;
                            padding: 0 10px;
                        }
                    }
                }
            }
            table.table1,table.table2{
                width: 100%;
                thead{
                    tr{
                        line-height: 1;
                        th{
                            width: 100%;
                            text-align: center;
                            line-height: 60px;
                            font-size: 20px;
                        }
                    } 
                }
                tbody{
                    tr{
                        font-size: 0;
                        position: relative;
                        td{
                            height: 50px;
                            &:first-child{
                                width: 10%;
                            }
                            
                        }
                        &:first-child{
                            background-color: #e5f3ff;
                            td{
                                text-align: center;
                                height: 48px;
                               
                            }
                        }
                        .removeTable{
                            position: absolute;
                            right: -52px;
                            margin-top: 10px;
                            .el-icon-remove-outline:before{
                                font-size: 30px;
                            }
                        }
                    }
                }
            }
            div.info{
                padding: 6.5px 0 1.5px;
                p{
                    font-size: 14px;
                    line-height: 21px;
                }
            }
            ul{
                padding: 4px 0;
                li{
                    font-size: 14px;
                    line-height: 21px;
                    text-decoration: none;
                    list-style: none;
                    &:first-child{
                        font-size: 20px;
                        line-height: 66px;
                        text-align: center;
                    }
                }
            }
            table.table3{
                width: 100%;
                thead{
                    tr{
                        line-height: 1;
                        th{
                            font-size: 20px;
                            line-height: 112px;
                        }
                    }
                }
                tbody{
                    tr{
                        td{
                            &:first-child{
                                width: 31.8%;
                                text-align: right;
                                background-color: #e5f3ff;
                            }
                            &:nth-child(3){
                                width: 22.7%;
                                text-align: right;
                                background-color: #e5f3ff;
                            }
                        }
                        &:last-child{
                            td:first-child{
                                vertical-align: text-top;
                                padding-top: 10px;
                            }
                        }
                    }
                }
            }
            .el-date-editor.el-input{
                width: 100%;
                .el-input__inner{
                    padding-left: 30px;
                }
                .el-input__icon{
                    line-height: 50px;
                }
            }
        }
        .button{
            text-align: center;
            .el-button{
                margin: 37px 0 27px;
            }
            
        }
    }
}
</style>



