<template>
    <div id="guideSummaryMessageBox">
        <!-- 搜索 -->
        <div class="queryForm">
            <el-form ref="queryForm" :model="queryForm">
                <el-form-item label="指南建议名称：" >
                    <el-input v-model="queryForm.guideName"></el-input>
                </el-form-item>
                <el-form-item label="所属领域：">
                    <el-select v-model="queryForm.domain" style="width:190px;">
                        <el-option v-for="(item,index) in optGroup1" :key="index" :label="item.content" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="所属类别：">
                    <el-select v-model="queryForm.category">
                        <el-option v-for="(item,index) in optGroup2" :key="index" :label="item.content" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="填报单位：">
                    <el-input v-model="queryForm.fillUnit"></el-input>
                </el-form-item>
                <el-form-item label="填报联系人：">
                    <el-input v-model="queryForm.fillContacts"  style="width:190px;"></el-input>
                </el-form-item>
                <el-form-item label="联系人手机：">
                    <el-input v-model="queryForm.contactPhone"></el-input>
                </el-form-item>
            </el-form>
            <el-button @click="handleSearch">搜索</el-button>
        </div>

        <!-- 展示列表 -->
        <div class="showList">
            <!-- 表格 -->
            <el-table
                v-loading="loading"
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                    type="selection"
                    align="center">
                </el-table-column>
                <el-table-column
                    type="index"
                    label="序号"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="指南建议名称"
                    :show-overflow-tooltip="true"
                    align="center">
                 <template slot-scope="scope">
                    {{ scope.row.guide_name }}
                </template>
                </el-table-column>
                <el-table-column
                    prop="domain"
                    label="所属领域"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="category"
                    label="所属类别"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="research_fund"
                    label="研究经费"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="research_period"
                    label="研究期限"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="fill_unit"
                    label="填报单位"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="fill_contacts"
                    label="填报联系人"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="contact_phone"
                    label="联系人手机"
                    align="center">
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange"
                @handleTableFreshNum="handleTableFresh">
            </pages>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'guideSummaryMessageBox',
        data() {
            return {
                selectedIDs: [],
                queryForm:{
                    guideName:'',
                    domain:'',
                    category:'',
                    fillUnit:'',
                    fillContacts:'',
                    contactPhone:'',
                    pageNum: 1,
                    pageSize: 10
                },
                optGroup1: [],
                optGroup2: [],
                loading: false,
                tableData: [],
                // multipleSelection: [],
                currentPage:4,
                fenye: {
                    total:400, //共有数据多少条
                    pageNum:1,
                    pageSize:10, //每页显示的条数
                    pageSizes:[10,20,30,40,50] //选择每页显示多少条
                }
            }
        },
        methods:{
            handleSelectionChange(val) {
                this.multipleSelection = val;
                let ids = [];
                this.multipleSelection.map((item)=> {
                    ids.push(item.id);
                })
                this.selectedIDs = ids;
                this.$emit("receipt",this.selectedIDs,this.tableData);
            },
            handleCurrentChange(val) {              //val表示当前页
                this.queryForm.pageNum = val;
                this._axios();
            },
            handleSizeChange(val){                  //val表示每页展示的条数
                this.queryForm.pageSize = val;
                this._axios();
            },
            handleTableFresh(){
                this._axios;
                document.querySelector(".first-pager").click();
            },
            _axios() {
                this.axios({
                    url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/getCollectionByParam',
                    method: 'get',
                    params: this.queryForm
                }).then((res) => {
                    this.loading = false;
                    let data = res.data.data;
                    if(data == "没有查到相关信息") {
                        this.tableData = []; 
                        this.$alert('没有查到相关信息','提示',{
                            confirmButtonText: '确定',
                            type: 'warning',
                            callback: action => {}
                        });
                    }else {
                        this.tableData = data;
                    }
                })
            },
            handleSearch() {
                this.loading = true;
                this.queryForm.pageNum = 1;
                this._axios();
                document.querySelector(".first-pager").click();
            }
        },
        beforeMount() {
            // 请求所属领域、所属类别
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/getCategoryAndDomain',
                method: 'get',
            }).then((res) => {
                let data = res.data.data;
                for(let i in data) {
                    if(data[i].classification == "所属领域") {
                        this.optGroup1.push(data[i]);
                    }else {
                        this.optGroup2.push(data[i]);
                    }
                }
            })
            this._axios();
        }
    }
</script>

<style lang="less">
    #guideSummaryMessageBox{
        background-color: #edf1f4;
        * {
           font-size: 14px; 
        }
        .queryForm {
            .el-form {
                padding: 5px 0 0 0;
                height: 90px;
                .el-form-item {
                    margin: 5px 0;
                    label {
                        width: 110px !important;
                    }
                }
            }
            .el-button {
                bottom: 10px;
                right: 35px;
            }
        }
        .showList {
            position: relative;
            height: 360px;
            min-height: 0 !important;
            background-color: #fff;
            margin-top: 5px;
            padding: 5px;
            
        }
        .el-table {
            min-height: 0 !important;
            padding-bottom: 0 !important;
            .has-gutter {
                width: 99%;
            }
            .el-table__body-wrapper {
                height: 315px;
                overflow-y: auto;
            }
            table {
                width: auto !important;
            }
            .el-table--scrollable-x,.el-table__body-wrapper{
                overflow-x: hidden;
            }
            &::before {
                background-color: #fff;
                height: 0;
            }
            td,th {
                padding: 6px 0;
            }
            td,th {
                border-left: 1px solid #EBEEF5;
                &:first-child{
                    border-right: 0;
                }
            }
        }
        .pages{
            // transform: scale(0.9);
            padding-top: 5px;
            border-top: 1px solid #d9d9d9;
            .el-button{
                background-color: transparent;
                border: 0;
                margin: 0;
                span{
                    line-height: 1;
                }
            }
        }
    }
</style>



