<template>
    <div id="resultsLibraryShow">
        <div class="showForm">
            <el-form :ref="showForm" :model="showForm">
                <table class="form_table">
                    <thead>
                        <tr><th colspan="4">成果库详情页</th></tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>课题名称：</td>
                            <td>
                                <el-input v-model="showForm.topicName"></el-input>
                            </td>
                            <td>课题编号：</td>
                            <td>
                                <el-input v-model="showForm.topicNumber"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>应用单位名称：</td>
                            <td colspan="3">
                                <el-input v-model="showForm.applicationUnitName"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>应用单位通讯地址（邮编）：</td>
                            <td colspan="3">
                                <el-input v-model="showForm.postalAddress"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>成果应用开始时间：</td>
                            <td>
                                <el-input v-model="showForm.achievementStartTime"></el-input>
                            </td>
                            <td>成果应用结束时间：</td>
                            <td>
                                <el-input v-model="showForm.achievementEndTime"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>应用情况、社会经济效益：</td>
                            <td colspan="3">
                                <el-input v-model="showForm.economicPerformance"></el-input>
                            </td>
                            
                        </tr>
                        <tr>
                            <td>成果信息附件：</td>
                            <td colspan="3">
                                <a  @click="handleDownload">{{showForm.achievementName}} </a>
                                <!-- <el-input v-model="showForm.achievementUrl"></el-input> -->
                            </td>
                        </tr>
                    </tbody>
                </table>
                <table class="form_table1" >
                    <!-- v-show="showForm.outcomeInformationPatentList.length>0" -->
                    <tbody>
                        <tr>
                            <td colspan="5" style="height:50px;text-align:center;">专利一览表：</td>
                        </tr>
                        <tr>
                            <td>序号</td>
                            <td>名称</td>
                            <td>专利申请时间</td>
                            <td>专利申请号</td>
                        </tr>
                        <tr v-for="(patent,i) in showForm.outcomeInformationPatentList">
                            <td>
                                <el-input v-model="showForm.outcomeInformationPatentList[i].serialNumber"></el-input>
                            </td>
                            <td>
                                <el-input v-model="showForm.outcomeInformationPatentList[i].outcomeName"></el-input>
                            </td>
                            <td>
                                <el-input v-model="showForm.outcomeInformationPatentList[i].patentApplicationTime"></el-input>
                            </td>
                            <td>
                                <el-input v-model="showForm.outcomeInformationPatentList[i].patentApplicationNumber"></el-input>
                            </td>
                        </tr>
                    </tbody>
                </table>
                <table class="form_table1" >
                    <!-- v-show="showForm.outcomeInformationPaperList.length>0" -->
                    <tbody>
                        <tr>
                            <td colspan="5" style="height:50px;text-align:center;">论文、专著一览表：</td>
                        </tr>
                        <tr>
                            <td><span>序号</span></td>
                            <td><span>刊物</span> </td>
                            <td><span>发表时间</span></td>
                            <td><span>作者</span></td>
                            <td><span>论文级别（SCI/EI/核心等）</span></td>
                        </tr>
                        <tr v-for="(paper,i) in showForm.outcomeInformationPaperList">
                            <td>
                                <el-input v-model="showForm.outcomeInformationPaperList[i].serialNumber"></el-input>
                            </td>
                            <td>
                                <el-input v-model="showForm.outcomeInformationPaperList[i].publication"></el-input>
                            </td>
                            <td>
                                <el-input v-model="showForm.outcomeInformationPaperList[i].publicationTime"></el-input>
                            </td>
                            <td>
                                <el-input v-model="showForm.outcomeInformationPaperList[i].author"></el-input>
                            </td>
                            <td>
                                <el-input v-model="showForm.outcomeInformationPaperList[i].paperLevel"></el-input>
                            </td>
                        </tr>
                        
                    </tbody>
                </table>
            </el-form>
            <el-button type="primary" @click="handleBack">返回</el-button>
        </div>
    </div>
</template>
<script>
export default {
    name:"resultsLibraryShow",
    data(){
        return{
            showForm:{
                topicName:'',
                topicNumber:'',
                applicationUnitName:'',
                postalAddress:'',
                achievementStartTime:'',
                achievementEndTime:'',
                economicPerformance:'',
                achievementUrl:'',
                outcomeInformationPatentList:[{
                    achievementsId:4,
                    id: 1,
                    outcomeName: "第一成果名称",
                    patentApplicationNumber: "第一专利号",
                    patentApplicationTime: "1745-12-24",
                    serialNumber: "115623",
                },{
                    achievementsId: 4,
                    id: 2,
                    outcomeName: "第二成果名称",
                    patentApplicationNumber: "第二专利号",
                    patentApplicationTime: "1745-12-24",
                    serialNumber: "1156156",
                },{
                    achievementsId: 4,
                    id: 3,
                    outcomeName: "第三成果名称",
                    patentApplicationNumber: "第三专利号",
                    patentApplicationTime: "1745-12-24",
                    serialNumber: "11516",
                }],//专利一览表
                outcomeInformationPaperList:[{
                    achievementsId: 4,
                    author: "第一作者",
                    id: 1,
                    name: "第一名称",
                    paperLevel: "第一论文级别",
                    publication: "第一刊物",
                    publicationTime: "1785-04-24",
                    serialNumber: "213544"
                },{
                    achievementsId: 4,
                    author: "第二作者",
                    id: 2,
                    name: "第二名称",
                    paperLevel: "第二论文级别",
                    publication: "第二刊物",
                    publicationTime: "1785-04-24",
                    serialNumber: "4456512"
                },{
                    achievementsId: 4,
                    author: "第三作者",
                    id: 3,
                    name: "第三名称",
                    paperLevel: "第三论文级别",
                    publication: "第三刊物",
                    publicationTime: "1785-04-24",
                    serialNumber: "213544"
                }],//论文专著一览表
            },
            paramsData:{
                id: this.$route.params.id,
                tableData:this.$route.params.arrays
            },
            file:[]
        }
    },
    methods:{
        handleBack(){
            this.$router.back(-1);
        },
        TicketParams(formData) { 
            if(formData != undefined){
                formData.map((item) => {
                    if(this.paramsData.id == item.id){
                        this.showForm = item
                        let obj = {
                            achievementName:item.achievementName,
                            achievementUrl:item.achievementUrl
                        }
                        this.file.push(obj)
                        return;
                    }
                })
            }
        },
        // 文件下载
        handleDownload(){
            let _this = this
            console.log(_this.file[0].achievementUrl)
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/file/queryFileStream',
                params:{
                    fileUrl:_this.file[0].achievementUrl,
                    fileName:_this.file[0].achievementName
                },
                headers:{
                     'Content-type':'application/x-www-form-urlencoded'
                }
            }).then(function(res){
                console.log(res)
            }).catch(function(err){

            })
        }
    },
    mounted(){
        console.log(this.paramsData.tableData)
        this.TicketParams(this.paramsData.tableData)
    }
}
</script>
<style lang="less">
#resultsLibraryShow{
    padding-bottom: 60px;
    background-color: #fff;
    margin-bottom: 20px;
    .showForm{
        .el-form{
            table.form_table,
            table.form_table1{
                min-width: 900px;
                @media  screen and ( max-width: 1600px ) {
                    width: 72.4%;
                }
                tbody{
                    tr{
                        td{
                            &:first-child{
                                @media screen and ( min-width: 1500px ) and ( max-width: 1850px ){
                                    width: 30.8%;
                                }
                                @media  screen and ( max-width: 1500px ) {
                                    width: 31.8%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
</style>



