<template>
    <div id="subjectApplyShow">
        <!-- 详情列表 -->
        <div class="showForm">
            <el-form ref="showForm" :model="showForm">
                <table class="form_table">
                    <thead>
                        <tr><th colspan="4">验收申请详情页</th></tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>课题名称：</td>
                            <td>
                                <el-input v-model="showForm.topicName" disabled></el-input>
                            </td>
                            <td>课题编号：</td>
                            <td>
                                <el-input v-model="showForm.topicNumber" disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>承担单位：</td>
                            <td colspan="3">
                                <el-input v-model="showForm.subjectUndertakingUnit" disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>单位性质：</td>
                            <td colspan="3">
                                <el-radio-group v-model="showForm.unitNature" >
                                    <span v-for="(item,index) in unitNatureOptions" :key = index>
                                        <el-radio disabled :label="item.id">{{item.content}}</el-radio>
                                    </span>
                                </el-radio-group>
                            </td>
                        </tr>
                        <tr>
                            <td>课题责任人：</td>
                            <td>
                                <el-input v-model="showForm.projectLeader" disabled></el-input>
                            </td>
                            <td>课题责任人联系电话：</td>
                            <td>
                                <el-input v-model="showForm.projectLeaderPhone" disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>课题责任人电子邮箱：</td>
                            <td>
                                <el-input v-model="showForm.projectLeaderMail" disabled></el-input>
                            </td>
                            <td>课题责任人通讯地址：</td>
                            <td>
                                <el-input v-model="showForm.postalAddress" disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>合同开始时间：</td>
                            <td>
                                <el-input v-model="showForm.agreementStartTime" disabled></el-input>
                            </td>
                            <td>合同结束时间：</td>
                            <td>
                                <el-input v-model="showForm.agreementEndTime" disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>验收申请时间：</td>
                            <td>
                                <el-input v-model="showForm.applicationAcceptanceTime" disabled></el-input>
                            </td>
                            <td>申请验收形式：</td>
                            <td>
                                <el-radio-group v-model="showForm.applicationAcceptanceMode" >
                                    <span v-for="(item,index) in applicationAcceptanceModeOptions" :key = index>
                                        <el-radio disabled :label="item.id">{{item.content}}</el-radio>
                                    </span>
                                </el-radio-group>
                            </td>
                        </tr>
                        <tr>
                            <td>申请验收地点：</td>
                            <td colspan="3">
                                <el-input v-model="showForm.applicationAcceptancePlace" disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>验收联系人：</td>
                            <td>
                                <el-input v-model="showForm.acceptanceContact" disabled></el-input>
                            </td>
                            <td>验收联系人联系电话：</td>
                            <td>
                                <el-input v-model="showForm.acceptanceContactPhone" disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>主要研究内容完成情况：</td>
                            <td colspan="3">
                                <el-input 
                                  type="textarea"
                                  v-model="showForm.mainContentSituation"
                                  disabled>
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>提交成果情况（合同规定提交成果完成情况及其它取得成果）：</td>
                            <td colspan="3">
                                <el-input 
                                 type="textarea"
                                 v-model="showForm.submissionAchievementsSituation"
                                 disabled>
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>验收提交资料清单：</td>
                            <td colspan="3"> 
                                <el-checkbox-group v-model="showForm.submitInventory">
                                    <span v-for="(item,index) in submitInventoryOptions" :key="index">
                                        <el-checkbox disabled :label="item.id">{{item.content}}</el-checkbox>
                                    </span>
                                </el-checkbox-group>    
                            </td>
                        </tr>
                        <tr>
                            <td>课题承担单位意见：</td>
                            <td colspan="3">
                                <el-input 
                                 type="textarea"
                                 v-model="showForm.subjectUndertakingUnitOpinion"
                                 disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>所在地环保部门意见：</td>
                            <td colspan="3">
                                <el-input 
                                 type="textarea"
                                 v-model="showForm.environmentalDepartmentsOpinion"
                                 disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>省生态环境评估中心初审意见：</td>
                            <td colspan="3">
                                <el-input 
                                 type="textarea"
                                 v-model="showForm.provinceAssessmentCenterOpinion"
                                 disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>省环保厅主管部门意见：</td>
                            <td colspan="3">
                                <el-input 
                                 type="textarea"
                                 v-model="showForm.competentDepartmentOinion"
                                 disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>验收申请表附件：</td>
                            <td colspan="3">
                                <el-input 
                                 v-model="showForm.applicationAcceptanceUrl"
                                 disabled></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>提交清单文件：</td>
                            <td colspan="3" style="height:50px;"> 
                                <el-input disabled v-model="showForm.submitInventoryUrl"></el-input>
                                <!-- <input type="file" id="submitInventoryFile"> -->
                            </td>
                        </tr>
                        <tr>
                            <td>验收申请表附件：</td>
                            <td colspan="3" style="height:50px;">
                                <el-input 
                                 disabled
                                 v-model="showForm.applicationAcceptanceUrl"
                                 ></el-input>
                                 <!-- <input type="file" id="applicationAcceptanceFile"> -->
                            </td>
                        </tr>
                        <tr>
                            <td>成果附件：</td>
                            <td colspan="3" style="height:50px;">
                                <el-input disabled v-model="showForm.achievementsUrl" ></el-input>
                                <!-- <a class='download' :href='showForm.achievementsUrl' download=""  title="下载">{{}}</a> -->
                                <!-- <input type="file" id="achievementsFile"> -->
                            </td>
                        </tr>
                    </tbody>
                 </table>

                 <!-- 审核流程 -->
                <table v-show="showForm.checkApplyStateList.length>0" class="form_table1">
                    <tr>
                        <td>交办人</td>
                        <td>处理人</td>
                        <td>审核步骤</td>
                        <td>交办时间</td>
                        <td>状态</td>
                        <td>处理内容</td>
                        <td>处理时间</td>
                    </tr>
                    <tr v-for="(item,index) in showForm.checkApplyStateList">
                        <td>
                            <el-input disabled v-model="item.fistHandler"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.secondHandler"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.auditStep"></el-input>
                        </td>
                        <td style="width:16%;">
                            <el-input disabled v-model="item.firstHandleTime"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.state"></el-input>
                        </td>
                        <td>
                            <el-input disabled v-model="item.handleContent"></el-input>
                        </td>
                        <td style="width:16%;">
                            <el-input disabled v-model="item.secondHandleTime"></el-input>
                        </td>
                    </tr>
                </table>
            </el-form>
            <el-button type="primary" v-show="exmine == 2" @click="handleExmine">审核</el-button>
            <el-button type="primary" v-show="exmine == 2" @click="handleReject">驳回</el-button>
            <el-button type="primary"  @click="handleBack">返回</el-button>
        </div>
        
        <!-- 上传附件弹框 对话框-->
        <el-dialog 
         :title="title"
         :visible.sync="dialogTableVisible" top="0">
             <el-form ref="form"  label-width="80px">
                 <el-form-item label="专项审计报告（事务所出具报告）：">
                    <!-- <el-input v-model="form.name"></el-input> -->
                    <!-- <el-upload
                        class="upload-demo"
                        action="https://jsonplaceholder.typicode.com/posts/"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        :before-remove="beforeRemove"
                        multiple
                        :limit="1"
                        :on-exceed="handleExceed"
                        :file-list="fileList">
                        <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload> -->
                    <input type="file" id="specialAuditFile">
                </el-form-item>
                <el-form-item label="初审报告：">
                    <!-- <el-input v-model="form.name"></el-input> -->
                    <input type="file" id="firstInspectionFile">
                </el-form-item>
             </el-form>
             <div class="button">
                <el-button type="primary" @click="handleConfirm">确认</el-button>
                <el-button type="primary" @click="handleCancel">取消</el-button>
             </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
    name:'subjectApplyShow',
    data(){
        return{
            showForm:{
                topicName:'1',
                topicNumber:'1',
                subjectUndertakingUnit:'1',
                unitNature:'1',
                projectLeader:'1',
                projectLeaderPhone:'1',
                projectLeaderMail:'1',
                postalAddress:'1',
                agreementStartTime:'1',
                agreementEndTime:'1',
                applicationAcceptanceTime:'1',
                applicationAcceptanceMode:[],
                applicationAcceptancePlace:'1',
                acceptanceContact:'1',
                acceptanceContactPhone:'1',
                mainContentSituation:'1',
                submissionAchievementsSituation:'1',
                submitInventory:'1',
                subjectUndertakingUnitOpinion:'1',
                environmentalDepartmentsOpinion:'1',
                provinceAssessmentCenterOpinion:'1',
                competentDepartmentOinion:'1',
                applicationAcceptanceUrl:'1',
                achievementsUrl:'1',
                checkApplyStateList:[{
                    auditStep:'',
                    checkApplyId:'',
                    firstHandleTime:'',
                    fistHandler:'',
                    handleContent:'',
                    id:'',
                    secondHandleTime:'',
                    secondHandler:'',
                    state:''
                }]
            },
            unitNatureOptions:[{//单位性质接口
                "id": 1,
                "classification": null,
                "content": "大专院校",
                "classificationId": null,
                "contentId": null,
                "state": null
            }],
            applicationAcceptanceModeOptions:[{//申请验收形式接口
                id:'',
                content:''
            }],
            submitInventoryOptions:[{//验收提交资料清单
                id:'',
                content:''
            }],
            paramsData: {
                id: this.$route.params.id,
                arrays: this.$route.params.arrays,
                isShowExmine:this.$route.params.isShowExmine
            },
            exmine:'',
            dialogTableVisible:false,
            title:'请上传附件'
        }
    },
    methods:{
        // 返回
        handleBack(){
            // this.$router.go(-1);//返回上一层
            this.$router.back(-1)
        },
        // 审核
        handleExmine(){
            this.dialogTableVisible = true
        },
        // 审核通过中的确认
        handleConfirm(){
            let specialAuditFile=document.getElementById('specialAuditFile').files[0]
            let firstInspectionFile=document.getElementById('firstInspectionFile').files[0]
            if(specialAuditFile == undefined || firstInspectionFile == undefined){
                this.$alert('请上传附件', '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {
                }).catch(() => {
                })
                return
            }
            this.submitFiles(specialAuditFile,firstInspectionFile)
        },
        // 审核确认之后提交文件
        submitFiles(specialAuditFile,firstInspectionFile){
            let _this = this
            let _id = _this.paramsData.id
            let formData = new FormData()
            formData.append('id',_id)
            formData.append('type',true)
            formData.append('specialAuditFile',specialAuditFile)
            formData.append('firstInspectionFile',firstInspectionFile)
            _this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/acceptState/examine',
                data:formData
            }).then(function(res){
                if(res.resultFlag == 1){
                    _this.$alert(res.data.data, '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                        _this.$router.push('/')
                    }).catch(() => {
                        _this.$router.push('/')
                    })
                }else{
                    _this.$alert('审核成功', '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                        _this.$router.back(0)
                    }).catch(() => {
                        _this.$router.back(0)
                    })
                }
            }).catch(function(err){
                console.log(err)
            })
        },
        // 审核通过中的取消
        handleCancel(){
            this.dialogTableVisible = false
        },
        // 驳回
        handleReject(){
            
        },
        // 单位性质字典类接口
        getUnitNature(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/unitNature',
            }).then(function(res){
                _this.unitNatureOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        //申请验收形式字典接口
        getApplicationAcceptanceModeOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationAcceptance',
            }).then(function(res){
                _this.applicationAcceptanceModeOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 验收提交资料清单接口
        getSubmitInventoryOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/checkApplyStyle/applicationSubmitList'
            }).then(function(res){
                _this.submitInventoryOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        TicketParams(formData) {
            if(formData != undefined){
                formData.map((item) => {
                    if(this.paramsData.id == item.id){
                        this.showForm = item
                        this.showForm.submitInventory = JSON.parse(item.submitInventory)
                        return;
                    }
                })
            }
        },
    },
    mounted(){
        this.getUnitNature()//单位性质
        this.getApplicationAcceptanceModeOptions()//申请验收形式
        this.getSubmitInventoryOptions()//验收提交资料清单接口
        this.exmine = this.paramsData.isShowExmine
        this.TicketParams(this.paramsData.arrays)
        // console.log(arrayID)
    }
}
</script>
<style lang="less">
#subjectApplyShow{
    .showForm{
        .el-form{
            .el-table{
                min-width: 900px;
            }
            .form_table1{
                margin-top: 40px;
                width: 70%;
                min-width: 100px;
                tr{
                    td{
                        border-left: 1px solid #e0e0e0;
                        border-bottom: 1px solid #e0e0e0;
                        height: 50px;
                        text-align: center;
                        &:last-child{
                            border-right: 1px solid #e0e0e0;
                        }
                    }
                    &:first-child{
                        border-top: 1px solid #e0e0e0;
                        td{
                            background-color:#e5f3ff;
                        }
                    }
                }
            }
            td{
                text-align: left;
            }
            .el-select{
                width: 100%;
            }
            .el-radio{
                line-height: 50px;
                margin: 0 10px;
                .el-radio__input{
                    padding-right: 5px;
                }
                .el-radio__input.is-+span.el-radio__label{
                    color: #000;
                }
                .el-radio__input.is-checked .el-radio__inner{
                    background-color: #0abd90;
                    border-color: #0abd90;
                }
                .el-radio__input.is-.is-checked .el-radio__inner::after{
                    background-color: #fff;
                }
            }
            textarea{
                min-height: 100px !important;
                padding: 0 10px;
            }
            .el-checkbox-group{
                >span{
                    margin:5px 10px;
                    display: inline-block;
                    .el-checkbox{
                        min-width: 120px;
                        
                        .el-checkbox__label{
                            display: inline-block;
                        }
                    } 
                }
            }
            .el-checkbox__label{
                margin-left: 5px;
            }
            #achievementsFile,#applicationAcceptanceFile,#submitInventoryFile{
                margin-left:10px;
            }
            .el-date-editor{
                .el-input__inner{
                    padding-left: 30px;
                }
                .el-input__prefix{
                    line-height: 50px;
                }
            }
        }
    }
    .el-dialog__wrapper{
        .el-dialog{
            margin-top: 0 !important;
            .el-dialog__body{
                .button{
                    text-align: center;
                    .el-button{
                        display:inline-block;
                        margin: auto;
                    }
                }
            }
        }
        .el-form{
            .el-form-item__label{
                width: 277px !important;
            }
            .el-form-item__content{
                margin-left: 277px !important;
            }
            textarea{
                min-height: 278px !important;
                padding: 10px;
            }
            
        }
        
        .el-dialog__footer{
            text-align: center;
        }
    }
}

</style>



