<template>
    <div id="guideSummaryShow">
        <h3 class="form_title">{{ paramsData.name }}</h3>
        <el-form ref="form">
            <table class="form_inner_table">
                <thead>
                    <tr>
                        <td>序号</td>
                        <td>指南建议名称</td>
                        <td>填报单位</td>
                        <td>单位类别</td>
                        <td>所属领域</td>
                        <td>填报类别</td>
                        <td>建议立项时间</td>
                        <td style="padding:0 97px;">建议理由及依据</td>
                        <td style="padding:0 65px;">主要研究内容和关键技术</td>
                        <td style="padding:0 75px;">预期目标和成果</td>
                        <td>拟出标准、技术规范、法规名称</td>
                        <td>经费测算（万元）</td>
                        <td>研究期限</td>
                        <td>示范工程规模</td>
                        <td>示范工程点</td>
                        <td>省内从事该领域的主要研究机构</td>
                        <td>填报联系人</td>
                        <td>联系电话（手机）</td>
                        <td style="padding:0 111px;">备注</td>
                        <td style="padding:0 95px;">查重结果</td>
                        <td style="padding:0 95px;">查重备注</td>
                    </tr>
                </thead>
                <tbody>
                    <tr class="classify_title" v-show="showTable1.length > 0">
                        <td colspan="21">一、综合示范类（{{ showTable1.length }}项）</td>
                    </tr>
                    <tr v-for="(item,index) in showTable1" :key="" class="classify_content">
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>{{ item.project_time }}</td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>{{ item.note }}</td>
                        <td>{{ item.check_back_result }}</td>
                        <td>{{ item.check_back_note }}</td>
                    </tr>
                    <tr class="classify_title" v-show="showTable2.length > 0">
                        <td colspan="21">
                            <span v-show="showTable1.length == 0">一、</span>
                            <span v-show="showTable1.length > 0">二、</span>重大技术公关类（{{ showTable2.length }}项）
                        </td>
                    </tr>
                    <tr v-for="(item,index) in showTable2" :key="index" class="classify_content">
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>{{ item.project_time }}</td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>{{ item.note }}</td>
                        <td>{{ item.check_back_result }}</td>
                        <td>{{ item.check_back_note }}</td>
                    </tr>
                    <tr class="classify_title" v-show="showTable3.length > 0">
                        <td colspan="21">
                            <span v-show="showTable1.length == 0 && showTable2.length == 0">一、</span>
                            <span v-show="(showTable1.length == 0 && showTable2.length > 0) || (showTable1.length > 0 && showTable2.length == 0)">二、</span>
                            <span v-show="showTable1.length > 0 && showTable2.length > 0">三、</span>技术研发类（{{ showTable3.length }}项）
                        </td>
                    </tr>
                    <tr v-for="(item,index) in showTable3" :key="index" class="classify_content">
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>{{ item.project_time }}</td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>{{ item.note }}</td>
                        <td>{{ item.check_back_result }}</td>
                        <td>{{ item.check_back_note }}</td>
                    </tr>
                </tbody>
            </table>
        </el-form>
        <div class="btn_group">
            <el-button class="btn_back" @click="handleBack">返回</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'guideSummaryShow',
        data() {
            return {
                paramsData: {
                    name: this.$route.params.id
                },
                chineseIndex: ['一','二','三'],
                showTable1: [],
                showTable2: [],
                showTable3: []
            }
        },
        methods: {
            handleBack() {
                this.$router.go(-1);
            }
        },
        mounted() {
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/getSummaryByTitle',
                method: 'get',
                params: {
                    guideSummaryTitle: this.paramsData.name
                }
            }).then((res) => {
                let data = res.data.data;
                for(let i in data) {
                    let category = data[i].ownership_category;
                    if(category == 76) {          //综合示范
                        this.showTable1.push(data[i])
                    }else if(category == 77) {    //技术研发
                        this.showTable2.push(data[i])
                    }else if(category == 78) {    //重大技术攻关
                        this.showTable3.push(data[i])
                    }
                }
            })
        }
    }
</script>

<style lang="less">
    #guideSummaryShow {
        min-height: 100%;
        background-color: #fff;
        overflow-y: hidden;
        padding: 0 20px;
        .el-form {
            height: 100%;
            overflow-x: auto;
            .form_inner_table {
                tbody {
                    .classify_content {
                        .text_center {
                            text-align: center;
                        }
                        td {
                            white-space: nowrap;
                            padding: 0 10px;
                        }
                    }
                }
            }
        }
        .btn_group {
            margin: 20px 0;
        }
    }
</style>



