<template>
    <div id="expertAddList">
        <div class="addForm">
            <el-form  ref="addForm" :model="addForm">
                <table class="form_table">
                    <thead>
                        <tr>
                            <th colspan="4">专家新增</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>姓名：</td>
                            <td>
                                <el-input 
                                 v-model="addForm.realName">
                                </el-input>
                            </td>
                            <td>登录名：</td>
                            <td>
                                <el-input 
                                 v-model="addForm.loginName">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>密码：</td>
                            <td>
                                <el-input
                                 v-model="addForm.password"></el-input>
                            </td>
                            <td>省内/省外：</td>
                            <td>
                                <el-select v-model="addForm.isProvince" placeholder="请选择">
