                                    v-for="item in addForm.isProvinceOptions"
                                    :key="item.id"
                                    :label="item.content"
                                    :value="item.id">
                                    </el-option>
                                </el-select>
                            </td>
                        </tr>
                        <tr>
                            <td>性别：</td>
                            <td>
                                <el-select v-model="addForm.sex" placeholder="请选择">
                                    <el-option
                                    v-for="item in addForm.sexOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label">
                                    </el-option>
                                </el-select>
                            </td>
                            <td>出生日期：</td>
                            <td>
                                <el-date-picker
                                 v-model="addForm.birthDate"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                        </tr>
                        <tr>
                            <td>学历：</td>
                            <td>
                                <el-select v-model="addForm.education" placeholder="请选择">
                                    <el-option
                                    v-for="item in addForm.eduOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label">
                                    </el-option>
                                </el-select>
                            </td>
                            <td>现任职务：</td>
                            <td>
                                <el-input
                                 v-model="addForm.presentPost"></el-input>
                            </td>
                            
                        </tr>
                        <tr>
                            <td>技术职称：</td>
                            <td>
                                <el-input
                                 v-model="addForm.technicalTitle"></el-input>
                            </td>
                            <td>所学专业：</td>
                            <td>
                                <el-input
                                 v-model="addForm.studyMajor"></el-input>
                            </td>
                            
                           
                        </tr>
                        <tr>
                            <td>从事专业：</td>
                            <td>
                                <el-input 
                                 v-model="addForm.professionalism"></el-input>
                            </td>
                            <td>工作单位：</td>
                            <td>
                                <el-input 
                                 v-model="addForm.workUnit"></el-input>
                            </td>
                            
                        </tr>
                        <tr>
                            <td>通讯地址：</td>
                            <td colspan="3">
                                <el-input 
                                 v-model="addForm.postalAddress"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>邮政编码：</td>
                            <td>
                                <el-input
                                 v-model="addForm.postalCode"></el-input>
                            </td>
                             <td>单位电话：</td>
                            <td>
                                <el-input
                                 v-model="addForm.workTelephone"></el-input>
                            </td>
                            
                        </tr>
                        <tr>
                            <td>手机：</td>
                            <td>
                                <el-input
                                 v-model="addForm.phone"></el-input>
                            </td>
                            <td>电子邮箱：</td>
                            <td>
                                <el-input
                                 v-model="addForm.mail"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>工作性质：</td>
                            <td colspan="3">
                                 <el-radio-group v-model="addForm.natureWork">
                                     <span v-for="(item,index) in addForm.natureWorkOptions">
                                        <el-radio :label="item.id" >{{item.content}}</el-radio>
                                     </span>
                                 </el-radio-group>
                            </td>
                            
                        </tr>
                        <tr >
                            <td>专业领域：</td>
                            <td colspan="3" >
                               <el-checkbox-group  
                                :max="3"
                                v-model="addForm.checkedItems"
                                >
                                    <span v-for="(item,index) in addForm.natures" >
                                        <el-checkbox  :label="item.id" :key="item.id" @change="handleChange(item,index)">{{item.content}}</el-checkbox>
                                    </span>
                                </el-checkbox-group>
                            </td>
                        </tr>
                        
                        <tr>
                            <td>附件：</td>
                            <td colspan="3" style="height:50px;">
                                <!-- <el-upload
                                    class="upload-demo"
                                    ref="upload"
                                    :action="url"
                                    :limit="1"
                                    :data="upData"
                                    :file-list="fileList"
                                    :with-credentials="true"
                                    :on-change="addFile"
                                    :on-success="handleSuccess"
                                    :auto-upload="false"
                                    :headers="header"
                                    accept=".doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document">
                                    <el-button size="small" type="primary">点击上传</el-button>
                                </el-upload> -->
                                <input type="file"  id="file">
                            </td>
                        </tr>
                    </tbody>
                </table>
                <table class="form_table1">
                    <tbody>
                        <tr>
                            <td :rowspan="DirectionNum">
                                <span>主要研究方向</span> 
                            </td>
                        </tr>
                        <tr v-for="(item,index) in expertInformationResearchDirectionList" >
                            <td>
                                <span>
                                    {{index+1}}
                                </span>
                            </td>
                            <td style="width:80.3%;"><el-input v-model="item.mainResearchDirections"></el-input></td>
                            <span class="remove" @click="handleRemoveDirectionItem(index)"><i class="el-icon-remove-outline"></i></span>
                        </tr>
                        <tr>
                            <td colspan="2"><span> 个人简历（教育背景、主要工作经历及工作业绩（300字））：</span></td>
                            <td>
                                <el-input type="textarea" v-model="addForm.curriculumVitae"></el-input>
                            </td>
                        </tr>
                    </tbody>
                    <span class="add" @click="handleAddDirectionItem"><i class="el-icon-circle-plus-outline"></i></span>
                    <!-- <el-button type="primary">添加</el-button> -->
                </table>
                <table class="form_table2">
                    <tbody style="position:relative;">
                        <tr>
                            <td :rowspan="CountNum" style="width:6.6%"><span>主要学术成就</span> </td>
                        </tr>
                        <tr>
                            <td :rowspan="ArticleNum" style="width:6.6%">文章</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>题目</td>
                            <td>作者排序</td>
                            <td>期刊名</td>
                            <td>时间</td>
                        </tr>
                        <span class="add" @click="handleAddArticleItem"><i class="el-icon-circle-plus-outline"></i></span>
                        <tr v-for="(item,index) in expertInformationArticleList" >
                            <td style="width:6.6%">{{index+1}}</td>
                            <td><el-input v-model="item.articleTitle"></el-input></td>
                            <td><el-input v-model="item.authorrRanking"></el-input></td>
                            <td><el-input v-model="item.titleArticlesPeriodicals"></el-input></td>
                            <td><el-date-picker
                                 v-model="item.articlesPublicationTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker></td>
                            <span class="remove" @click="handleRemoveArticleItem(index)"><i class="el-icon-remove-outline"></i></span>
                        </tr>
                        <tr>
                            <td :rowspan="BookNum" style="width:6.6%">著作</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>名称</td>
                            <td>排序</td>
                            <td>出版社</td>
                            <td>时间</td>
                        </tr>
                        <span class="add" @click="handleAddBookItem"><i class="el-icon-circle-plus-outline"></i></span>
                        <tr v-for="(item,index) in expertInformationBookList" >
                            <td style="width:6.6%"><span>{{index+1}}</span></td>
                            <td><el-input v-model="item.workName"></el-input></td>
                            <td><el-input v-model="item.sort"></el-input></td>
                            <td><el-input v-model="item.bookPublishingHouse"></el-input></td>
                            <td>
                                <el-date-picker
                                 v-model="item.writingTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                            <span class="remove" @click="handleRemoveBookItem(index)"><i class="el-icon-remove-outline"></i></span>
                        </tr>
                        <tr>
                            <td :rowspan="PatentNum" style="width:6.6%">专利</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>名称</td>
                            <td>专利类型</td>
                            <td>专利号</td>
                            <td>时间</td>
                        </tr>
                        <span class="add" @click="handleAddPatentItem"><i class="el-icon-circle-plus-outline"></i></span>
                        <tr v-for="(item,index) in expertInformationPatentList" >
                            <td style="width:6.6%"><span>{{index+1}}</span></td>
                            <td><el-input v-model="item.patentName"></el-input></td>
                            <td><el-input v-model="item.patentTypes"></el-input></td>
                            <td><el-input v-model="item.patentNo"></el-input></td>
                            <td>
                                <el-date-picker
                                 v-model="item.patentTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                            <span class="remove" @click="handleRemovePatentItem(index)"><i class="el-icon-remove-outline"></i></span>
                        </tr>
                        <tr>
                            <td :rowspan="PrizeNum" style="width:6.6%">获奖</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>名称</td>
                            <td>排序</td>
                            <td>颁奖部门</td>
                            <td>时间</td>
                        </tr>
                        <span class="add" @click="handleAddPrizeItem"><i class="el-icon-circle-plus-outline"></i></span>
                        <tr v-for="(item,index) in expertInformationPrizeWinningList" >
                            <td style="width:6.6%"><span>{{index+1}}</span></td>
                            <td><el-input v-model="item.prizeWinningName"></el-input></td>
                            <td><el-input v-model="item.prizeWinningOrder"></el-input></td>
                            <td><el-input v-model="item.prizeWinningDepartment"></el-input></td>
                            <td>
                                <el-date-picker
                                 v-model="item.prizeWinningTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                            <span class="remove" @click="handleRemovePrizeItem(index)"><i class="el-icon-remove-outline"></i></span>
                        </tr>
                    </tbody>
                </table>
            </el-form>
            <el-button @click="handleConfirm">提交</el-button>
        </div>
    </div>
</template>
<script>
export default {
    name:'expertAddList',
    data(){
        return{
            addForm:{
                realName:'',
                loginName:'',
                password:'',
                isProvinceOptions:[{
                    classification:null,
                    classificationId:null,
                    content:"省内专家",
                    contentId:null,
                    id:89,
                    state:null,
                },{
                    classification: null,
                    classificationId: null,
                    content: "省外专家",
                    contentId: null,
                    id: 90,
                    state: null,
                }],
                isProvince:'',
                sexOptions:[{
                    id:'0',
                    value: '0',
                    label: '女'
                },{
                    id:'1',
                    value: '1',
                    label: '男'
                }],
                sex:'',
                birthDate:'',
                eduOptions:[{
                    id:'0',
                    value: '0',
                    label: '大专'
                },{
                    id:'1',
                    value: '1',
                    label: '本科'
                }],
                education:'',
                presentPost:'',
                technicalTitle:'',
                studyMajor:'',
                professionalism:'',
                workUnit:'',
                postalAddress:'',
                postalCode:'',
                workTelephone:'',
                phone:'',
                mail:'',
                natureWorkOptions:[{
                    classification: null,
                    classificationId: null,
                    content: "科研(教育)",
                    contentId: null,
                    id: 30,
                    state: null
                },{
                    classification: null,
                    classificationId: null,
                    content: "咨询(设计、规划)",
                    contentId: null,
                    id: 31,
                    state: null
                },{
                    classification: null,
                    classificationId: null,
                    content:"管理及其他",
                    contentId: null,
                    id: 32,
                    state: null
                }],
                natureWork:0,
                checkedItems:[],
                natures:[],
                professionalField:'',
                curriculumVitae:'',
            },
            fileList:[],
            DirectionNum:0,
            CountNum:0,
            ArticleNum:0,
            BookNum:0,
            PatentNum:0,
            PrizeNum:0,
            expertInformationResearchDirectionList:[{
                mainResearchDirections:''
            },{
                mainResearchDirections:''
            },{
                mainResearchDirections:''
            }],
            expertInformationArticleList:[{
                articleTitle:'1',
                authorrRanking:'1',
                titleArticlesPeriodicals:'1',
                articlesPublicationTime:'2019-08-09'
            },{
                articleTitle:'1',
                authorrRanking:'1',
                titleArticlesPeriodicals:'1',
                articlesPublicationTime:'2019-08-07'
            },{
                articleTitle:'',
                authorrRanking:'',
                titleArticlesPeriodicals:'',
                articlesPublicationTime:''
            }],
            expertInformationBookList:[{
                workName:'',
                sort:'',
                bookPublishingHouse:'',
                writingTime:'',
            },{
                workName:'',
                sort:'',
                bookPublishingHouse:'',
                writingTime:'',
            },{
                workName:'',
                sort:'',
                bookPublishingHouse:'',
                writingTime:'',
            }],
            expertInformationPatentList:[{
                Id:'',
                expertId:'',
                patentName:'',
                patentTypes:'',
                patentNo:'',
                patentTime:'',
            },{
                Id:'',
                expertId:'',
                patentName:'',
                patentTypes:'',
                patentNo:'',
                patentTime:'',
            },{
                Id:'',
                expertId:'',
                patentName:'',
                patentTypes:'',
                patentNo:'',
                patentTime:'',
            }],
            expertInformationPrizeWinningList:[{
                prizeWinningName:'',
                prizeWinningOrder:'',
                prizeWinningDepartment:'',
                prizeWinningTime:'',
            },{
                prizeWinningName:'',
                prizeWinningOrder:'',
                prizeWinningDepartment:'',
                prizeWinningTime:'',
            },{
                prizeWinningName:'',
                prizeWinningOrder:'',
                prizeWinningDepartment:'',
                prizeWinningTime:'',
            }],
        }
    },
    computed:{
    },
    methods:{
         // 添加主要研究方向
        handleAddDirectionItem(){
            let obj={
                mainResearchDirections:''
            }
            this.expertInformationResearchDirectionList.push(obj)
            this.DirectionNum = this.expertInformationResearchDirectionList.length+1
        },
        handleRemoveDirectionItem(val){
            if(this.expertInformationResearchDirectionList.length < 4){
                this.$alert('最少保留三行', '提示', {
                    confirmButtonText: '确定',
                    type: 'warning',
                    callback: action => {}
                });
                return false;
            }else{
                this.DirectionNum = this.expertInformationResearchDirectionList.length
                this.expertInformationResearchDirectionList.splice(val,1)
            }
        },
        // 文章
        handleAddArticleItem(){
            let obj={
                articleTitle:'',
                authorrRanking:'',
                titleArticlesPeriodicals:'',
                articlesPublicationTime:''
            }
            this.expertInformationArticleList.push(obj)
            this.ArticleNum = this.expertInformationArticleList.length+3
            this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
        },
        handleRemoveArticleItem(val){
            if(this.expertInformationArticleList.length < 6){
                this.$alert('最少保留五行', '提示', {
                    confirmButtonText: '确定',
                    type: 'warning',
                    callback: action => {}
                });
                return false;
            }else{
                this.ArticleNum = this.expertInformationArticleList.length+2
                this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
                this.expertInformationArticleList.splice(val,1)
            }
        },
        // 著作
        handleAddBookItem(){
            let obj={
                workName:'',
                sort:'',
                bookPublishingHouse:'',
                writingTime:'',
            }
            this.expertInformationBookList.push(obj)
            this.BookNum = this.expertInformationBookList.length+3
            this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
        },
        handleRemoveBookItem(val){
            if(this.expertInformationBookList.length < 6){
                this.$alert('最少保留五行', '提示', {
                    confirmButtonText: '确定',
                    type: 'warning',
                    callback: action => {}
                });
                return false;
            }else{
                this.BookNum = this.expertInformationBookList.length+2
                this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
                this.expertInformationBookList.splice(val,1)
            }
        },
        // 专利
        handleAddPatentItem(){
            let obj={
                Id:'',
                expertId:'',
                patentName:'',
                patentTypes:'',
                patentNo:'',
                patentTime:'',
            }
            this.expertInformationPatentList.push(obj)
            this.PatentNum = this.expertInformationPatentList.length+3
            this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
        },
        handleRemovePatentItem(val){
            if(this.expertInformationPatentList.length < 6){
                this.$alert('最少保留五行', '提示', {
                    confirmButtonText: '确定',
                    type: 'warning',
                    callback: action => {}
                });
                return false;
            }else{
                this.PatentNum = this.expertInformationPatentList.length+2
                this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
                this.expertInformationPatentList.splice(val,1)
            }
        },
        // 获奖
        handleAddPrizeItem(){
            let obj={
                mainResearchDirections:''
            }
            this.expertInformationPrizeWinningList.push(obj)
            this.PrizeNum = this.expertInformationPrizeWinningList.length+3
            this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
        },
        handleRemovePrizeItem(val){
            if(this.expertInformationPrizeWinningList.length < 6){
                this.$alert('最少保留五行', '提示', {
                    confirmButtonText: '确定',
                    type: 'warning',
                    callback: action => {}
                });
                return false;
            }else{
                this.PrizeNum = this.expertInformationPrizeWinningList.length+2
                this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
                this.expertInformationPrizeWinningList.splice(val,1)
            }
        },
        // 主要学术成就的非空判断
        emptyJudgment(json){
            let arr =[]
            let $json = []
            for(let i=0;i<json.length;i++){
                $.each(json[i],function(index,val){
                    if(val != ''){
                        arr.push(i)
                        return false
                    }
                })
            }
            for(let j=0;j<arr.length;j++){
                $json.push(json[arr[j]]);
            }
            return $json
        },
        info($json){
            if($json.length > 0){
                for(let i in $json){
                    for(let j in $json[i]){
                        if($json[i][j] == '' || $json[i][j] == undefined){
                            return 'str'
                        }
                    }
                }
                return $json
            }else{
                return $json
            }
        },
        // 提交
        handleConfirm(){
            let Direction = this.emptyJudgment(this.expertInformationResearchDirectionList)
            let Article = this.info(this.emptyJudgment(this.expertInformationArticleList))
            let Book = this.info(this.emptyJudgment(this.expertInformationBookList))
            let Patent = this.info(this.emptyJudgment(this.expertInformationPatentList))
            let Prize  =this.info(this.emptyJudgment(this.expertInformationPrizeWinningList))
            if(Article == 'str' || Book == 'str' || Patent == 'str' || Prize == 'str'){
                this.$alert('请将主要学术成就部分补填完整', '提示', {
                    confirmButtonText: '确定',
                    callback:action => {}
                });
                return false
            }
            for(let i in this.addForm) {
                if(typeof(this.addForm[i]) == "string") {
                    if(this.addForm[i].match(/^[ ]*$/) || this.addForm[i] == 'null'){
                        this.$alert('请将表格填写完整', '提示', {
                            confirmButtonText: '确定',
                            type: 'warning',
                            callback: action => {}
                        });
                        return false;
                    }
                }
                if(typeof(this.addForm[i]) == "object") {
                    if(this.addForm[i].length == 0) {
                        this.$alert('请至少选择一类', '提示', {
                            confirmButtonText: '确定',
                            callback: action => {}
                        });
                        return false;
                    }
                }
            }
            this.submitAxios(Direction,Article,Book,Patent,Prize);
        },
        // 多选专业领域
        handleChange (data,index) {
            let str=data.id + ','
            if($('.el-checkbox-group>span').eq(index).find('label.el-checkbox').hasClass('is-checked')){
                this.addForm.professionalField = this.addForm.professionalField.replace(str,'')  
            }else{
                this.addForm.professionalField += str
            }
        },
        //提交数据
        submitAxios(Direction,Article,Book,Patent,Prize){
            // 从sessionStorage获取数据
            // var _loginName = sessionStorage.getItem('username');
            let _this = this
            var formData = new FormData();
            //文件部分
            var file = document.getElementById("file").files[0];
            formData.append("expertFile",file);
            console.log(file)
            let userInformation = JSON.stringify({
                'realName':_this.addForm.realName,
                'loginName':_this.addForm.loginName,
                'password':_this.addForm.password,
                'expertInformation':{
                    'sex':_this.addForm.sex,
                    'birthDate':_this.addForm.birthDate,
                    'education':_this.addForm.education,
                    'presentPost':_this.addForm.presentPost,
                    'technicalTitle':_this.addForm.technicalTitle,
                    'studyMajor':_this.addForm.studyMajor,
                    'professionalism':_this.addForm.professionalism,
                    'workUnit':_this.addForm.workUnit,
                    'postalAddress':_this.addForm.postalAddress,
                    'postalCode':_this.addForm.postalCode,
                    'workTelephone':_this.addForm.workTelephone,
                    'phone':_this.addForm.phone,
                    'mail':_this.addForm.mail,
                    'natureWork':_this.addForm.natureWork,
                    'professionalField':_this.addForm.professionalField,
                    'isProvince':_this.addForm.isProvince,
                    'curriculumVitae':_this.addForm.curriculumVitae,
                    'recommendationUnitOpinion':'1',
                    'expertInformationUrl':'2',
                    'expertInformationResearchDirectionList':Direction,
                    'expertInformationArticleList':Article,
                    'expertInformationBookList':Book,
                    'expertInformationPatentList':Patent,
                    'expertInformationPrizeWinningList':Prize
                }
            })
            formData.append("userInformation",new Blob([userInformation], {type: "application/json"}))
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expert/distributionExpertAccount',
                //忽略contentType
                contentType: false,
                //取消序列换 formData本来就是序列化好的
                processData: false,
                // data:_this.qs.stringify({
                //     'ExpertFile':file,
                //     'userInformation':$userInformation
                // }, {
                //     indices: false 
                // }),
                data:formData,
                usecredensives:true
            }).then(function(res){
                console.log(res)
                 _this.$alert('添加成功', '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {

                }).catch(() => {
                })
            }).catch(function(err){
                console.log(err)
            })
        },
        // 获取省内外专家
        getIsProvinceOptions(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expertStyle/province',
            }).then(function(res){
                _this.isProvinceOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 获取工作性质
        getNatureWork(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expertStyle/natureWork',
            }).then(function(res){
                _this.addForm.natureWorkOptions = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 获取专业领域
        getField(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expertStyle/professionalField',
            }).then(function(res){
              _this.addForm.natures = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        }
    },
    mounted(){
        this.DirectionNum = this.expertInformationResearchDirectionList.length+1
        this.ArticleNum = this.expertInformationArticleList.length+3
        this.BookNum = this.expertInformationBookList.length+3
        this.PatentNum = this.expertInformationPatentList.length+3
        this.PrizeNum = this.expertInformationPrizeWinningList.length+3
        this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
        this.getIsProvinceOptions()
        this.getNatureWork()
        this.getField()
    }
}
</script>
<style lang="less">
#expertAddList{
    .addForm{
        position: relative;
        min-height: 800px;
        background-color: #fff;
        text-align: center;
        @media  screen and ( max-width: 1500px ) {
            min-height: 650px;
        }
        .el-select{
            width: 100%;
        }
        table.form_table{
            width: 59.4%;
            margin: auto;
            border-collapse: collapse;
            @media  screen and ( max-width: 1600px ) {
                width: 72.4%;
            }
            thead{
                th{
                    font-size: 18px;
                    font-weight: normal;
                    height: 70px;
                    line-height: 70px;
                }
            }
            tbody{
                tr{
                    border-top: 1px solid #e0e0e0;
                    &:last-child{
                        border-bottom: 1px solid #e0e0e0;
                    }
                    td{
                        border-right: 1px solid #e0e0e0;
                        &:first-child{
                            width: 19.8%;
                            text-align: right;
                            border-left: 1px solid #e0e0e0;
                            background-color: #e5f3ff;
                        }
                        &:nth-child(3){
                            width: 19.7%;
                            text-align: right;
                            background-color: #e5f3ff;
                        }
                    }
                }
            }
        }
        table.form_table1{
            width: 59.4%;
            margin: auto;
            border-collapse: collapse;
            position: relative;
            @media  screen and ( max-width: 1600px ) {
                width: 72.4%;
            }
             tbody{
                 tr{
                    border-top: 1px solid #e0e0e0;
                    position: relative;
                    td{
                        height: 50px;
                        line-height: 50px;
                        border-right: 1px solid #e0e0e0;
                        position: relative;
                        &:first-child,
                        &:nth-child(2){
                            width:9.9%;
                            text-align: right;
                            border-left: 1px solid #e0e0e0;
                            border-bottom: 1px solid #e0e0e0;
                            background-color: #e5f3ff;
                            span{
                                width: 16px;
                                display: inline-block;
                                position: absolute;
                                top: 0;
                                left: 0;
                                bottom: 0;
                                right: 0;
                                margin: auto;
                                line-height: 1.2;
                                height: 16px;
                            }
                        }
                    }
                    &:first-child{
                        td:first-child{
                            span{
                                height: 115px;
                            }
                        }
                    }
                    &:last-child{
                        border-bottom: 1px solid #e0e0e0;
                        td{
                            font-size: 0;
                            span{
                                line-height:1;
                                width:auto;
                                padding:10px;
                                position: initial;
                                height: auto;
                                vertical-align: middle;
                            }
                        }
                    }
                 }
                 span.remove{
                     position: absolute;
                     margin-top: 10px;
                     right:-40px;
                     i::before{
                         font-size: 30px;
                     }
                 }
             }
             span.add{
                position: absolute;
                top: 10px;
                right: -75px;
                .el-icon-circle-plus-outline{
                     font-size: 30px;
                }
             }
        }
        table.form_table2{
             width: 59.4%;
            margin: auto;
            border-collapse: collapse;
            position: relative;
            @media  screen and ( max-width: 1600px ) {
                width: 72.4%;
            }
            tr{
                    border-top: 1px solid #e0e0e0;
                    position: relative;
                    td{
                        height: 50px;
                        line-height: 50px;
                        border-right: 1px solid #e0e0e0;
                        position: relative;
                        background-color: #e5f3ff;
                        &:first-child,
                        &:nth-child(2){
                            width:9.9%;
                            text-align: center;
                            border-left: 1px solid #e0e0e0;
                            border-bottom: 1px solid #e0e0e0;
                            background-color: #e5f3ff;
                        }
                    }
                    &:last-child{
                        border-bottom: 1px solid #e0e0e0;
                    }
             }
            span.remove{
                position: absolute;
                margin-top: 10px;
                right:-40px;
                i::before{
                    font-size: 30px;
                }
            }
            span.add{
                position: absolute;
                margin-top: 10px;
                right: -75px;
                .el-icon-circle-plus-outline{
                     font-size: 30px;
                }
             }
        
        }
        .el-input__inner{
            border: 0px;
            height: 50px;
            line-height: 50px;
        }
        .el-radio{
            line-height: 50px;
            margin: 0 10px;
            .el-radio__input{
                padding-right: 5px;
            }
            .el-radio__input.is-disabled+span.el-radio__label{
                color: #000;
            }
            .el-radio__input.is-checked .el-radio__inner{
                background-color: #0abd90;
                border-color: #0abd90;
            }
            .el-radio__input.is-disabled.is-checked .el-radio__inner::after{
                background-color: #fff;
            }
        }
        input:disabled,textarea:disabled{
            color: #000;
            background-color: #fff;
        }
        
        .el-textarea{
            height: 100%;
            textarea{
                border: 0px;
                height: 100% !important;
            }
        }
        .el-date-editor{
            width: 100%;
            .el-input__inner{
                padding-left:30px;
            }
        }
        .el-input__icon{
            line-height: 50px;
        }
        .upload-demo{
            overflow: hidden;
            ul.el-upload-list{
                float: left;
                padding-left: 10px;
                a.el-upload-list__item-name{
                    line-height: 32px;
                    padding-right: 40px;
                    text-align: left;
                }
                label.el-upload-list__item-status-label{
                    line-height: 32px;
                }
            }
        }
        .el-checkbox-group{
            padding: 10px;
            display: flex;
            flex-flow: wrap;
            > span{
                display: inline-block;
                margin: 0 10px;
                line-height: 20px;
                min-width: 140px;
                text-align: left;
                &>.el-checkbox .el-checkbox__label{
                    display: inline-block;
                    margin-left: 10px;
                }
            }
        }
    }
}
</style>




