<template>
    <div id="noticeShow">
        <div class="button">
            <el-button type="primary" @click="handleAdd">添加</el-button>
            <el-button type="primary" @click="handleEdit">修改</el-button>
            <el-button type="primary" @click="handleDelete">删除</el-button>
        </div>
        <!-- 展示列表 -->
        <div class="showList">
            <!-- 表格 -->
            <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                v-loading="loading"
                @selection-change="handleSelectionChange">
                <el-table-column
                type="selection"
                align="center">
                </el-table-column>
                <el-table-column
                type="index"
                label="序号"
                align="center">
                </el-table-column>
                <el-table-column
                prop="title"
                label="公告标题"
                :show-overflow-tooltip="true"
                align="center">
                 <template slot-scope="scope">
                    <!-- <router-link :to="{
                        name:'ContractCenterShow',
                        params:{
                            name:scope.row.id
                        }
                        }">  -->
                       <span @click="handleShow(scope.row.id)">{{scope.row.title}}</span> 
                    <!-- </router-link> -->
                </template>
                </el-table-column>
                <el-table-column
                prop="startTime"
                label="发送时间"
                align="center">
                </el-table-column>
                <el-table-column
                prop="content"
                min-width="135"
                label="公告内容"
                align="center">
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange"
                @handleTableFreshNum="handleTableFresh"
            ></pages>
        </div>

        <!-- 对话框 添加-->
        <el-dialog title="添加公告" :visible.sync="dialogTableVisible" top="0">
             <el-form ref="form" :model="form" label-width="80px">
                 <div class="form-item">
                     <span>公告标题：</span>
                     <el-input v-model="form.title"></el-input>
                 </div>
                <div class="form-item">
                    <div class="form-item-date">
                        <span>开始时间：</span>
                        <el-date-picker 
                        type="date" 
                        placeholder="选择日期" 
                        v-model="form.startTime"
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd" ></el-date-picker>
                        
                    </div>
                    <div class="form-item-date">
                        <span style="padding-left:10px;">结束时间：</span>
                        <el-date-picker 
                        type="date" 
                        placeholder="选择日期" 
                        v-model="form.endTime"
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd" ></el-date-picker>
                    </div>
                </div>
                <div class="form-item form-item-content">
                    <span>公告正文：</span>
                    <el-input
                     v-model="form.content"
                     type="textarea">
                    </el-input>
                </div>
            </el-form>
            <el-button type="primary"  @click="handleSend">发送</el-button>
        </el-dialog>
        <!-- 修改 -->
        <el-dialog title="修改公告" :visible.sync="dialogTableEdit" top="0">
             <el-form ref="form" :model="form" label-width="80px">
                 <div class="form-item">
                     <span>公告标题：</span>
                     <el-input v-model="form.title"></el-input>
                 </div>
                <div class="form-item">
                    <div class="form-item-date">
                        <span>开始时间：</span>
                        <el-date-picker 
                        type="date" 
                        placeholder="选择日期" 
                        v-model="form.startTime"
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd" ></el-date-picker>
                        
                    </div>
                    <div class="form-item-date">
                        <span style="padding-left:10px;">结束时间：</span>
                        <el-date-picker 
                        type="date" 
                        placeholder="选择日期" 
                        v-model="form.endTime"
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd" ></el-date-picker>
                    </div>
                </div>
                <div class="form-item form-item-content">
                    <span>公告正文：</span>
                    <el-input
                     v-model="form.content"
                     type="textarea">
                    </el-input>
                </div>
            </el-form>
            <el-button type="primary" @click="handleEditShow">修改</el-button>
        </el-dialog>
        <!-- 查看 -->
        <el-dialog title="查看公告" :visible.sync="dialogShow" top="0">
             <el-form ref="form" :model="form" label-width="80px">
                 <div class="form-item">
                     <span>公告标题：</span>
                     <el-input v-model="form.title" disabled></el-input>
                 </div>
                <div class="form-item">
                    <div class="form-item-date">
                        <span>开始时间：</span>
                        <el-date-picker 
                        type="date" 
                        placeholder="选择日期" 
                        v-model="form.startTime"
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd"
                        disabled ></el-date-picker>
                        
                    </div>
                    <div class="form-item-date">
                        <span style="padding-left:10px;">结束时间：</span>
                        <el-date-picker 
                        type="date" 
                        placeholder="选择日期" 
                        v-model="form.endTime"
                        format="yyyy-MM-dd"
                        value-format="yyyy-MM-dd"
                        disabled ></el-date-picker>
                    </div>
                </div>
                <div class="form-item form-item-content">
                    <span>公告正文：</span>
                    <el-input
                     v-model="form.content"
                     disabled
                     type="textarea">
                    </el-input>
                </div>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name:'noticeShow',
    data(){
        return{
            tableData:[{
                id:'0101',
                title:'公告',
                startTime:'11',
                content:'11'
            },{
                id:'0102',
                title:'公告',
                startTime:'11',
                content:'11'
            }],
            fenye:{
                total:400, //共有数据多少条
                pageNum:1,
                pageSize:2, //每页显示的条数
                pageSizes:[2,20,30,40] //选择每页显示多少条
            },
            dialogTableVisible:false,
            dialogTableEdit:false,
            dialogShow:false,
            form:{
                title:'',
                startTime:'',
                endTime:'',
                content:''
            },
            id:'',
            selectedIDs:[],
            multipleSelection:[],
            loading:true
        }
    },
    methods:{
        handleAdd(){
            let $this = this;
            $this.form.title=''
            $this.form.startTime=''
            $this.form.endTime=''
            $this.form.content=''
            this.dialogTableVisible = true
        },
        // 修改公告
        handleEdit(){
            let $this = this;
            if($this.selectedIDs.length != 1){
                $this.$alert('请选择一条数据进行修改', '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {
                }).catch(() => {
                })
            }else{
                $this.dialogTableEdit=true
                $this.id=$this.multipleSelection[0].id
                $this.form.title=$this.multipleSelection[0].title
                $this.form.startTime=$this.multipleSelection[0].startTime
                $this.form.endTime=$this.multipleSelection[0].endTime
                $this.form.content=$this.multipleSelection[0].content
            }
        },
        // 删除公告
        handleDelete(){
            let $this = this;
            if($this.selectedIDs.length < 1){
                $this.$alert('至少选择一条数据进行删除', '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {
                }).catch(() => {
                })
            }else{
                let arrIDs = JSON.parse(JSON.stringify($this.selectedIDs));//把数据转化后赋予this.datalist
                $this.deleteTableData(arrIDs)
                
            }
        },
        // 查看公告
        handleShow(id){
            this.tableData.map((item) => {
                if(id == item.id){
                    this.form = item
                    this.dialogShow = true
                    return;
                }
            })
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            var ids = []
            this.multipleSelection.map((item)=> {
                ids.push(item.id)
            })
            this.selectedIDs = ids
        },
        // 分页
        handleCurrentChange:function(val){//val表示当前页
            this.fenye.pageNum = val
            this.getTableData(this.fenye.pageNum,this.fenye.pageSize)
        },
        handleSizeChange(val){//val表示每页展示的条数
            this.fenye.pageSize = val
            this.getTableData(this.fenye.pageNum,this.fenye.pageSize)
        },
        handleTableFresh(){
            document.querySelector(".first-pager").click()
            this.getTableData(this.fenye.pageNum,this.fenye.pageSize)
        },
        handleSend(){//添加发送
            let _this = this;
            for(let i in _this.form){
                if(_this.form[i] == '' || _this.form[i] == null){
                    _this.$alert('请把信息填写完整', '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                    }).catch(() => {
                    })
                    return;
                }
            }
            if(_this.form.startTime > _this.form.endTime){
                _this.$alert('结束时间必须大于开始时间', '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                    }).catch(() => {
                    })
                return;
            }
            _this.addTableData(_this.form.title,_this.form.content,_this.form.startTime,_this.form.endTime)
        },
        addTableData(title,content,startTime,endTime){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/notification/add',
                params:{
                    title:title,
                    content:content,
                    startTime:startTime,
                    endTime:endTime
                },
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded'
                }
            }).then(function(res){
                _this.$alert('添加成功', '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                        _this.handleTableFresh()
                        _this.dialogTableVisible = false
                    }).catch(() => {
                        _this.handleTableFresh()
                        _this.dialogTableVisible = false
                    })
            }).catch(function(err){
                console.log(err)
            })
        },
        handleEditShow(){//公告修改
            let _this = this;
            for(let i in _this.form){
                if(_this.form[i] == '' || _this.form[i] == null){
                    _this.$alert('请把信息填写完整', '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                    }).catch(() => {
                    })
                    return;
                }
            }
            if(_this.form.startTime > _this.form.endTime){
                _this.$alert('结束时间必须大于开始时间', '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                    }).catch(() => {
                    })
                return;
            }
            _this.editTableData(_this.id,_this.form.title,_this.form.content,_this.form.startTime,_this.form.endTime)
        },
        editTableData(id,title,content,startTime,endTime){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/notification/update',
                params:{
                    nid:id,
                    title:title,
                    content:content,
                    startTime:startTime,
                    endTime:endTime
                },
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded'
                }
            }).then(function(res){
                _this.$alert('修改成功', '提示', {
                        confirmButtonText: '确定',
                        type:'warning',
                    }).then(() => {
                        _this.handleTableFresh()
                        _this.dialogTableEdit = false
                    }).catch(() => {
                        _this.handleTableFresh()
                        _this.dialogTableEdit = false
                    })
            }).catch(function(err){
                console.log(err)
            })
        },
        deleteTableData(arrIDs){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/notification/delete',
                data:_this.qs.stringify(
                    { 
                     ids:arrIDs,
                     type:1
                    },{ 
                        indices: false 
                    }),
                headers:{
                    'Content-type':'application/x-www-form-urlencoded'
                }
            }).then(function(res){
                _this.$alert('删除成功', '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {
                    document.querySelector(".first-pager").click()
                    _this.getTableData(_this.fenye.pageNum,_this.fenye.pageSize)
                }).catch(() => {
                    document.querySelector(".first-pager").click()
                    _this.getTableData(_this.fenye.pageNum,_this.fenye.pageSize)
                })
            }).catch(function(err){
                console.log(err)
            })
        },
        getTableData(page,total){
            let _this = this;
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/notification/query',
                params:{
                    page:page,
                    total:total
                }
            }).then(function(res){
                console.log(res.data.data)
                if(res.data.data == null){
                    _this.tableData=[]
                    _this.loading = false
                }else{
                    _this.loading = false
                    _this.fenye.total = res.data.data.alltotal
                    _this.tableData = res.data.data.data
                }
            }).catch(function(err){
                console.log(err)
            })
        }
    },
    mounted(){
        this.getTableData(this.fenye.pageNum,this.fenye.pageSize)
    }
}
</script>
<style lang="less">
#noticeShow{
    .pages{
        .el-button{
            background-color: transparent;
            border: 0;
            margin: 0;
            span{
                line-height: 1;
            }
        }
    }
}
</style>



