<template>
    <div id="midTermAdd">
        <el-form ref="form">
            <h3 class="form_title">
                <el-form-item label="中期检查名称：">
                    <el-input></el-input>
                </el-form-item>
            </h3>
            <div class="table_title">
                <h4>课题列表</h4>
                <el-button @click="handleAdd">添加</el-button>
            </div>
            <el-table
                ref="multipleTable"
                :data="tableIDs"
                tooltip-effect="dark">
                <el-table-column
                    type="index"
                    label="序号"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="data1"
                    label="课题名称"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="data2"
                    label="课题的目标和主要研究内容"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="data3"
                    label="合同开始时间"
                    align="center">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center">
                    <template>
                        <el-button @click="handleDelete">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-form>
        <div class="btn_group">
            <el-button class="btn_submit" @click="handleSubmit">提交</el-button>
            <el-button class="btn_back" @click="handleBack">返回</el-button>
        </div>
        <div class="cover_box" :class="{over_box_active:overBoxHide}">
            <div class="message_box">
                <span class="btn_close" @click="handleCloseCover"></span>
                <header class="message_box_header">
                    <h2 class="title">合同添加</h2>
                </header>
                <section class="message_box_content" v-if="refreshFlag">
                    <messageBox @receipt="receiptChildInfo"></messageBox>
                </section>
                <div class="btn_group">
                    <el-button @click="handleConfirmCover">确定</el-button>
                    <el-button @click="handleCancelCover">取消</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import MidTermMessageBox from '@/components/daliyManage/midTermMessageBox'
    export default {
        name: 'midTermAdd',
        components: {
            messageBox: MidTermMessageBox
        },
        data() {
            return {
                overBoxHide: true,
                refreshFlag: true,
                selectedIDs: [],
                tableIDs: [],
                tableData: []
            }
        },
        methods: {
            handleSubmit() {
                const loading = this.$loading({
                    lock: true,
                    text: '请稍后...',
                    spinner: 'el-icon-loading',
                    background: 'rgba(255,255,255,0.7)'
                });
                setTimeout(() => {
                    loading.close();
                    this.$alert('提交成功', '提示', {
                        confirmButtonText: '确定',
                        type: 'success',
                        callback: action => {
                            this.$router.go(-1);
                        }
                    });
                },2000);
            },
            handleBack() {
                this.$router.go(-1);
            },
            handleAdd(val) {
                this.overBoxHide = false;
            },
            handleDelete(val) {
                alert("delete")
            },
            receiptChildInfo(val) {
                this.selectedIDs = val;
            },
            handleCloseCover() {
                this.overBoxHide = true;
            },
            handleConfirmCover() {
                if(this.selectedIDs.length == 0) {
                    this.$alert('请选择一条数据', '提示', {
                        confirmButtonText: '确定',
                        type: 'warning',
                        callback: action => {}
                    });
                }else {
                    // 判断是否重复
                    for(let i in this.tableIDs) {
                        for(let j in this.selectedIDs) {
                            if(this.tableIDs[i] == this.selectedIDs[j]) {
                                this.$alert('请勿重复选择', '提示', {
                                    confirmButtonText: '确定',
                                    type: 'warning',
                                    callback: action => {}
                                });
                                return false;
                            }
                        }
                    }
                    // 正常操作
                    this.overBoxHide = true;
                    this.tableIDs.push(...this.selectedIDs);
                    this.selectedIDs = [];
                    // 刷新弹窗
                    this.refreshFlag = false;
                    setTimeout(() => {
						this.refreshFlag = true;
					},0);
                }
            },
            handleCancelCover() {
                this.overBoxHide = true;
                // 刷新弹窗
                this.refreshFlag = false;
                setTimeout(() => {
                    this.refreshFlag = true;
                },0);
            },
        }
    }
</script>

<style lang="less">
    #midTermAdd {
        min-height: 100%;
        background-color: #fff;
        overflow-y: hidden;
        padding: 0 20px;
        .el-form {
            height: 100%;
            overflow-x: auto;
            .form_title {
                .el-form-item {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .el-input {
                        width: 320px;
                    }
                }
            }
            .table_title {
                width: 1100px;
                margin: auto;
                h4 {
                    display: inline-block;
                    font-size: 20px;
                    font-weight: normal;
                    margin: 0 20px 0 10px;
                }
            }
            .el-table {
                width: 1100px;
                margin: auto;
                border-right: 1px solid #e0e0e0;
                border-collapse: collapse;
                tr {
                    height: 50px;
                    th {
                        background-color: #e5f3ff;
                    }
                }
                .el-table__header-wrapper {
                    table {
                        border-collapse: collapse;
                        th {
                            border: 1px solid #e0e0e0;
                            border-bottom: none;
                        }
                    }
                }
                .el-table__body-wrapper {
                    table {
                        border-collapse: collapse;
                        td {
                            border: 1px solid #e0e0e0;
                        }
                    }
                }
            }
            .form_inner_table {
                tbody {
                    .classify_title {
                        >:first-child {
                            text-align: center;
                        }
                        td {
                            >span {
                                display: inline-block;
                                width: 215px;
                            }
                        }
                    }
                }
            }
        }
        .btn_group {
            margin: 20px 0;
        }
        
    }
</style>



