<template>
    <div id="expertLibraryShow">
        <div class="addForm">
            <el-form  ref="addForm" :model="addForm">
                <table class="form_table">
                    <thead>
                        <tr>
                            <th colspan="4">专家库详情页</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>姓名：</td>
                            <td>
                                <el-input 
                                disabled
                                 v-model="addForm.realName">
                                </el-input>
                            </td>
                            <td>登录名：</td>
                            <td>
                                <el-input 
                                 disabled
                                 v-model="addForm.loginName">
                                </el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>省内/省外：</td>
                            <td>
                                <el-select 
                                 disabled
                                 v-model="addForm.expertInformation.isProvince" 
                                 placeholder="请选择">
                                    <el-option
                                    v-for="item in addForm.expertInformation.isProvinceOptions"
                                    :key="item.id"
                                    :label="item.content"
                                    :value="item.id">
                                    </el-option>
                                </el-select>
                            </td>
                            <td>性别：</td>
                            <td>
                                <el-select 
                                 disabled
                                 v-model="addForm.expertInformation.sex" 
                                 placeholder="请选择">
                                    <el-option
                                    v-for="item in addForm.expertInformation.sexOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label">
                                    </el-option>
                                </el-select>
                            </td>
                        </tr>
                        <tr>
                            <td>出生日期：</td>
                            <td>
                                <el-date-picker
                                 disabled
                                 v-model="addForm.expertInformation.birthDate"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                            <td>学历：</td>
                            <td>
                                <el-select 
                                 disabled
                                 v-model="addForm.expertInformation.education" 
                                 placeholder="请选择">
                                    <el-option
                                    v-for="item in addForm.eduOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.label">
                                    </el-option>
                                </el-select>
                            </td>
                        </tr>
                        <tr>
                            <td>现任职务：</td>
                            <td>
                                <el-input
                                 disabled
                                 v-model="addForm.expertInformation.presentPost"></el-input>
                            </td>
                            <td>技术职称：</td>
                            <td>
                                <el-input
                                 disabled
                                 v-model="addForm.expertInformation.technicalTitle"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>所学专业：</td>
                            <td>
                                <el-input
                                 disabled
                                 v-model="addForm.expertInformation.studyMajor"></el-input>
                            </td>
                            <td>从事专业：</td>
                            <td>
                                <el-input 
                                 disabled
                                 v-model="addForm.expertInformation.professionalism"></el-input>
                            </td>
                           
                        </tr>
                        <tr>
                            <td>工作单位：</td>
                            <td colspan="3">
                                <el-input 
                                 disabled
                                 v-model="addForm.expertInformation.workUnit"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>通讯地址：</td>
                            <td colspan="3">
                                <el-input 
                                 disabled
                                 v-model="addForm.expertInformation.postalAddress"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>邮政编码：</td>
                            <td>
                                <el-input
                                 disabled
                                 v-model="addForm.expertInformation.postalCode"></el-input>
                            </td>
                             <td>单位电话：</td>
                            <td>
                                <el-input
                                 disabled
                                 v-model="addForm.expertInformation.workTelephone"></el-input>
                            </td>
                            
                        </tr>
                        <tr>
                            <td>手机：</td>
                            <td>
                                <el-input
                                 disabled
                                 v-model="addForm.expertInformation.phone"></el-input>
                            </td>
                            <td>电子邮箱：</td>
                            <td>
                                <el-input
                                 disabled
                                 v-model="addForm.expertInformation.mail"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>工作性质：</td>
                            <td colspan="3">
                                 <el-radio-group v-model="addForm.expertInformation.natureWork">
                                     <span v-for="(item,index) in natureWorkOptions">
                                        <el-radio disabled :label="item.id" >{{item.content}}</el-radio>
                                     </span>
                                 </el-radio-group>
                            </td>
                            
                        </tr>
                        <tr >
                            <td>专业领域：</td>
                            <td colspan="3" >
                               <el-checkbox-group  
                                v-model="curriculumVitae"
                                >
                                    <span v-for="(item,index) in natures">
                                        <el-checkbox  :label="item.id" :key="item.id" disabled>{{item.content}}</el-checkbox>
                                    </span>
                                </el-checkbox-group>
                            </td>
                        </tr>
                        
                        <tr>
                            <td>附件：</td>
                            <td colspan="3" style="height:50px;">
                                <!-- <el-upload
                                    class="upload-demo"
                                    ref="upload"
                                    :action="url"
                                    :limit="1"
                                    :data="upData"
                                    :file-list="fileList"
                                    :with-credentials="true"
                                    :on-change="addFile"
                                    :on-success="handleSuccess"
                                    :auto-upload="false"
                                    :headers="header"
                                    accept=".doc,.docx,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document">
                                    <el-button size="small" type="primary">点击上传</el-button>
                                </el-upload> -->
                                <!-- <input type="file"  id="file"> -->
                                <!-- <a :href="addForm.expertInformation.expertInformationUrl" download="addForm.expertInformation.expertInformationUrl">{{addForm.expertInformation.expertInformationUrl}}</a> -->
                                <a href="http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/test/bbbb" download="新建文本文档1111.zip"  >{{addForm.expertInformation.expertInformationUrl}}</a>
                                
                            </td>
                        </tr>
                    </tbody>
                </table>
                <table class="form_table1">
                    <tbody>
                        <tr>
                            <td :rowspan="DirectionNum">
                                <span>主要研究方向</span> 
                            </td>
                        </tr>
                        <tr v-for="(item,index) in addForm.expertInformation.expertInformationResearchDirectionList">
                            <td>
                                <span>
                                    {{index+1}}
                                </span>
                            </td>
                            <td style="width:80.3%;"><el-input disabled v-model="item.mainResearchDirections"></el-input></td>
                        </tr>
                        <tr>
                            <td colspan="2"><span> 个人简历（教育背景、主要工作经历及工作业绩（300字））：</span></td>
                            <td>
                                <el-input type="textarea" disabled v-model="addForm.expertInformation.curriculumVitae"></el-input>
                            </td>
                        </tr>
                    </tbody>
                </table>
                <table class="form_table2">
                    <tbody>
                        <tr>
                            <td :rowspan="CountNum" style="width:6.6%"><span>主要学术成就</span> </td>
                        </tr>
                        <tr>
                            <td :rowspan="ArticleNum" style="width:6.6%">文章</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>题目</td>
                            <td>作者排序</td>
                            <td>期刊名</td>
                            <td>时间</td>
                        </tr>
                        <tr v-for="(item,index) in addForm.expertInformation.expertInformationArticleList">
                            <td style="width:6.6%">{{index+1}}</td>
                            <td><el-input disabled v-model="item.articleTitle"></el-input></td>
                            <td><el-input disabled v-model="item.authorrRanking"></el-input></td>
                            <td><el-input disabled v-model="item.titleArticlesPeriodicals"></el-input></td>
                            <td><el-date-picker
                                 disabled
                                 v-model="item.articlesPublicationTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker></td>
                        </tr>
                        <tr>
                            <td :rowspan="BookNum" style="width:6.6%">著作</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>名称</td>
                            <td>排序</td>
                            <td>出版社</td>
                            <td>时间</td>
                        </tr>
                        <tr v-for="(item,index) in addForm.expertInformation.expertInformationBookList">
                            <td style="width:6.6%"><span>{{index+1}}</span></td>
                            <td><el-input disabled v-model="item.workName"></el-input></td>
                            <td><el-input disabled v-model="item.sort"></el-input></td>
                            <td><el-input disabled v-model="item.bookPublishingHouse"></el-input></td>
                            <td>
                                <el-date-picker
                                 disabled
                                 v-model="item.writingTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                        </tr>
                        <tr>
                            <td :rowspan="PatentNum" style="width:6.6%">专利</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>名称</td>
                            <td>专利类型</td>
                            <td>专利号</td>
                            <td>时间</td>
                        </tr>
                        <tr v-for="(item,index) in addForm.expertInformation.expertInformationPatentList">
                            <td style="width:6.6%"><span>{{index+1}}</span></td>
                            <td><el-input disabled v-model="item.patentName"></el-input></td>
                            <td><el-input disabled v-model="item.patentTypes"></el-input></td>
                            <td><el-input disabled v-model="item.patentNo"></el-input></td>
                            <td>
                                <el-date-picker
                                 disabled
                                 v-model="item.patentTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                        </tr>
                        <tr>
                            <td :rowspan="PrizeNum" style="width:6.6%">获奖</td>
                        </tr>
                        <tr>
                            <td style="width:6.6%"></td>
                            <td>名称</td>
                            <td>排序</td>
                            <td>颁奖部门</td>
                            <td>时间</td>
                        </tr>
                        <tr v-for="(item,index) in addForm.expertInformation.expertInformationPrizeWinningList">
                            <td style="width:6.6%"><span>{{index+1}}</span></td>
                            <td><el-input disabled v-model="item.prizeWinningName"></el-input></td>
                            <td><el-input disabled v-model="item.prizeWinningOrder"></el-input></td>
                            <td><el-input disabled v-model="item.prizeWinningDepartment"></el-input></td>
                            <td>
                                <el-date-picker
                                 disabled
                                 v-model="item.prizeWinningTime"
                                 type="date"
                                 value-format="yyyy-MM-dd"
                                 placeholder="选择日期">
                                </el-date-picker>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </el-form>
            <el-button v-show="this.paramsData.isShow == 1" @click="handleAdopt">通过</el-button>
            <el-button v-show="this.paramsData.isShow == 1" @click="handleReject">驳回</el-button>
            <el-button @click="handleBack">返回</el-button>
        </div>
        <!-- 驳回弹框 -->
        <el-dialog
            title="请输入退回理由"
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            <el-form ref="form" :model="form" :rules="rules" label-width="80px">
                <el-form-item label="请输入驳回理由：" prop="reason">
                    <el-input 
                     type="textarea" 
                     v-model="form.reason"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleBtnConfirm('form')">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
const natureOptions = [{
                        id:'1',
                        content:'水环境治理'
                    },{
                        id:'2',
                        content:'大气环境保护'
                    },{
                        id:'3',
                        content:'土壤环境治理'
                    },{
                        id:'4',
                        content:'固体废物处置'
                    },{
                        id:'5',
                        content:'自然生态保护'
                    },{
                        id:'6',
                        content:'环境监测监控'
                    },{
                        id:'7',
                        content:'核与辐射防治'
                    },{
                        id:'8',
                        content:'环保仪器装备'
                    },{
                        id:'9',
                        content:'规划与战略研究'
                    },{
                        id:'10',
                        content:'污染预防与控制（清洁生产）'
                    },{
                        id:'11',
                        content:'政策法规'
                    },{
                        id:'12',
                        content:'环境标准及其他'
                    }]
export default {
    name:'expertLibraryShow',
    data(){
        return{
            addForm:{
                realName:'',
                loginName:'',
                expertInformation:{
                    isProvinceOptions:[{
                        id:'89',
                        content:'省内专家'
                    },{
                        id:'90',
                        content:'省外专家'
                    }],
                    isProvince:'',
                    sexOptions:[{
                        id:'0',
                        value: '0',
                        label: '女'
                    },{
                        id:'1',
                        value: '1',
                        label: '男'
                    }],
                    sex:'',
                    birthDate:'',
                    eduOptions:[{
                        id:'0',
                        value: '0',
                        label: '大专'
                    },{
                        id:'1',
                        value: '1',
                        label: '本科'
                    }],
                    education:'',
                    presentPost:'',
                    technicalTitle:'',
                    studyMajor:'',
                    professionalism:'',
                    workUnit:'',
                    postalAddress:'',
                    postalCode:'',
                    workTelephone:'',
                    phone:'',
                    mail:'',
                    natureWork:null,
                    professionalField:'',
                    expertInformationResearchDirectionList:[{
                        mainResearchDirections:''
                    },{
                        mainResearchDirections:''
                    },{
                        mainResearchDirections:''
                    }],
                    expertInformationArticleList:[{
                        articleTitle:'1',
                        authorrRanking:'1',
                        titleArticlesPeriodicals:'1',
                        articlesPublicationTime:'2019-08-09'
                    },{
                        articleTitle:'1',
                        authorrRanking:'1',
                        titleArticlesPeriodicals:'1',
                        articlesPublicationTime:'2019-08-07'
                    },{
                        articleTitle:'',
                        authorrRanking:'',
                        titleArticlesPeriodicals:'',
                        articlesPublicationTime:''
                    }],
                    expertInformationBookList:[{
                        workName:'',
                        sort:'',
                        bookPublishingHouse:'',
                        writingTime:'',
                    },{
                        workName:'',
                        sort:'',
                        bookPublishingHouse:'',
                        writingTime:'',
                    },{
                        workName:'',
                        sort:'',
                        bookPublishingHouse:'',
                        writingTime:'',
                    }],
                    expertInformationPatentList:[{
                        Id:'',
                        expertId:'',
                        patentName:'',
                        patentTypes:'',
                        patentNo:'',
                        patentTime:'',
                    },{
                        Id:'',
                        expertId:'',
                        patentName:'',
                        patentTypes:'',
                        patentNo:'',
                        patentTime:'',
                    },{
                        Id:'',
                        expertId:'',
                        patentName:'',
                        patentTypes:'',
                        patentNo:'',
                        patentTime:'',
                    }],
                    expertInformationPrizeWinningList:[{
                        prizeWinningName:'',
                        prizeWinningOrder:'',
                        prizeWinningDepartment:'',
                        prizeWinningTime:'',
                    },{
                        prizeWinningName:'',
                        prizeWinningOrder:'',
                        prizeWinningDepartment:'',
                        prizeWinningTime:'',
                    },{
                        prizeWinningName:'',
                        prizeWinningOrder:'',
                        prizeWinningDepartment:'',
                        prizeWinningTime:'',
                    }],
                }
            },
            curriculumVitae:[],
            natures:[],
            natureWorkOptions:[{
                classification: null,
                classificationId: null,
                content: "科研(教育)",
                contentId: null,
                id:30,
                state: null
            },{
                classification: null,
                classificationId: null,
                content: "咨询(设计、规划)",
                contentId: null,
                id:31,
                state: null
            },{
                classification: null,
                classificationId: null,
                content:"管理及其他",
                contentId: null,
                id:32,
                state: null
            }],
            fileList:[],
            DirectionNum:0,
            ArticleNum:0,
            BookNum:0,
            PatentNum:0,
            PrizeNum:0,
            CountNum:0,
            paramsData:{
                id:this.$route.params.id,
                tableData:this.$route.params.arrays,
                isShow:this.$route.params.isShow
            },
            form:{//退回弹框字段
                reason:'',
            },
            rules: {//表单验证
                reason:[
                    { required: true, message: '请输入驳回理由', trigger: 'blur' }
                ],
            },
            dialogVisible:false //显示退回弹出框
        }
    },
    computed:{
    },
    methods:{
        // 通过
        handleAdopt(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expert/examine',
                params:{
                    type:true,
                    id:_this.paramsData.id
                }
            }).then(function(res){
                _this.$alert('审核通过', '提示', {
                    confirmButtonText: '确定',
                    type:'warning',
                }).then(() => {
                    _this.$router.back(0)
                }).catch(() => {
                    _this.$router.back(0)
                })
            }).catch(function(err){
                console.log(err)
            })
        },
        // 驳回
        handleReject(){
            this.dialogVisible = true
        },
        handleClose(){//弹框的关闭按钮点击事件
            this.dialogVisible = false
        },
        // 弹框的确认驳回
        handleBtnConfirm(formReason){
            let _this = this
            _this.$refs[formReason].validate((valid) => {
                if (valid) {
                   this.axios({
                        method:'POST',
                        url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expert/examine',
                        params:{
                            type:false,
                            reason:_this.form.reason,
                            id:_this.paramsData.id
                        }
                    }).then(function(res){
                        _this.$alert('审核成功', '提示', {
                            confirmButtonText: '确定',
                            type:'warning',
                        }).then(() => {
                            _this.$router.back(0)
                        }).catch(() => {
                            _this.$router.back(0)
                        })
                    }).catch(function(err){
                        console.log(err)
                    })
                } else {
                    return false;
                }
            });
        },
        // 返回
        handleBack(){
            this.$router.back(-1);
        },
        TicketParams(formData) { 
            if(formData != undefined){
                formData.map((item) => {
                    if(this.paramsData.id == item.uid){
                        this.getFieldArr(item)
                        this.addForm = item
                        this.addForm.expertInformation.natureWork = parseInt(item.expertInformation.natureWork)
                        return;
                    }
                })
            }
        },
        // 专业领域对应字段
        getFieldArr(item){
            var arr = item.expertInformation.professionalField.split(',');
            arr.splice(arr.length-1,1)
            for(let i in arr){
                arr[i] = parseInt(arr[i])
            }
            this.curriculumVitae = arr
        },
        // 获取专业领域
        getField(){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/expertStyle/professionalField',
            }).then(function(res){
              _this.natures = res.data.data
            }).catch(function(err){
                console.log(err)
            })
        },
        // 下载文件
        handleDownload(){
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/test/bbbb'
            }).then(function(res){
                console.log(res)
            }).catch(function(err){
                console.log(err)
            })
        }
    },
    mounted(){
        this.TicketParams(this.paramsData.tableData)
        this.getField()
        this.DirectionNum = this.addForm.expertInformation.expertInformationResearchDirectionList.length+1
        this.ArticleNum = this.addForm.expertInformation.expertInformationArticleList.length+2
        this.BookNum = this.addForm.expertInformation.expertInformationBookList.length+2
        this.PatentNum = this.addForm.expertInformation.expertInformationPatentList.length+2
        this.PrizeNum = this.addForm.expertInformation.expertInformationPrizeWinningList.length+2
        this.CountNum = this.ArticleNum + this.BookNum + this.PatentNum + this.PrizeNum +1
    }
}
</script>
<style lang="less">
#expertLibraryShow{
    .addForm{
        position: relative;
        min-height: 800px;
        background-color: #fff;
        text-align: center;
        @media  screen and ( max-width: 1500px ) {
            min-height: 650px;
        }
        .el-select{
            width: 100%;
        }
        table.form_table{
            width: 59.4%;
            margin: auto;
            border-collapse: collapse;
            @media  screen and ( max-width: 1600px ) {
                width: 72.4%;
            }
            thead{
                th{
                    font-size: 18px;
                    font-weight: normal;
                    height: 70px;
                    line-height: 70px;
                }
            }
            tbody{
                tr{
                    border-top: 1px solid #e0e0e0;
                    &:last-child{
                        border-bottom: 1px solid #e0e0e0;
                    }
                    td{
                        border-right: 1px solid #e0e0e0;
                        &:first-child{
                            width: 19.8%;
                            text-align: right;
                            border-left: 1px solid #e0e0e0;
                            background-color: #e5f3ff;
                        }
                        &:nth-child(3){
                            width: 19.7%;
                            text-align: right;
                            background-color: #e5f3ff;
                        }
                    }
                }
            }
        }
        table.form_table1{
            width: 59.4%;
            margin: auto;
            border-collapse: collapse;
            position: relative;
            @media  screen and ( max-width: 1600px ) {
                width: 72.4%;
            }
             tbody{
                 tr{
                    border-top: 1px solid #e0e0e0;
                    position: relative;
                    td{
                        height: 50px;
                        line-height: 50px;
                        border-right: 1px solid #e0e0e0;
                        position: relative;
                        &:first-child,
                        &:nth-child(2){
                            width:9.9%;
                            text-align: right;
                            border-left: 1px solid #e0e0e0;
                            border-bottom: 1px solid #e0e0e0;
                            background-color: #e5f3ff;
                            span{
                                width: 16px;
                                display: inline-block;
                                position: absolute;
                                top: 0;
                                left: 0;
                                bottom: 0;
                                right: 0;
                                margin: auto;
                                line-height: 1.2;
                                height: 16px;
                            }
                        }
                    }
                    &:first-child{
                        td:first-child{
                            span{
                                height: 115px;
                            }
                        }
                    }
                    &:last-child{
                        border-bottom: 1px solid #e0e0e0;
                        td{
                            font-size: 0;
                            span{
                                line-height:1;
                                width:auto;
                                padding:10px;
                                position: initial;
                                height: auto;
                                vertical-align: middle;
                            }
                        }
                    }
                 }
                 span.remove{
                     position: absolute;
                     margin-top: 10px;
                     right:-40px;
                     i::before{
                         font-size: 30px;
                     }
                 }
             }
             .el-button{
                 position: absolute;
                 top: 0;
                 right: -122px;
             }
        }
        table.form_table2{
             width: 59.4%;
            margin: auto;
            border-collapse: collapse;
            position: relative;
            @media  screen and ( max-width: 1600px ) {
                width: 72.4%;
            }
            tr{
                    border-top: 1px solid #e0e0e0;
                    position: relative;
                    td{
                        height: 50px;
                        line-height: 50px;
                        border-right: 1px solid #e0e0e0;
                        position: relative;
                        background-color: #e5f3ff;
                        &:first-child,
                        &:nth-child(2){
                            width:9.9%;
                            text-align: center;
                            border-left: 1px solid #e0e0e0;
                            border-bottom: 1px solid #e0e0e0;
                            background-color: #e5f3ff;
                        }
                    }
                    &:last-child{
                        border-bottom: 1px solid #e0e0e0;
                    }
             }
        }
        .el-input__inner{
            border: 0px;
            height: 50px;
            line-height: 50px;
        }
        .el-radio{
            line-height: 50px;
            margin: 0 10px;
            .el-radio__input{
                padding-right: 5px;
            }
            .el-radio__input.is-disabled+span.el-radio__label{
                color: #000;
            }
            .el-radio__input.is-checked .el-radio__inner{
                background-color: #0abd90;
                border-color: #0abd90;
            }
            .el-radio__input.is-disabled.is-checked .el-radio__inner::after{
                background-color: #fff;
            }
        }
        input:disabled,textarea:disabled{
            color: #000;
            background-color: #fff;
        }
        
        .el-textarea{
            height: 100%;
            textarea{
                border: 0px;
                height: 100% !important;
            }
        }
        .el-date-editor{
            width: 100%;
            .el-input__inner{
                padding-left:30px;
            }
        }
        .el-input__icon{
            line-height: 50px;
        }
        .upload-demo{
            overflow: hidden;
            ul.el-upload-list{
                float: left;
                padding-left: 10px;
                a.el-upload-list__item-name{
                    line-height: 32px;
                    padding-right: 40px;
                    text-align: left;
                }
                label.el-upload-list__item-status-label{
                    line-height: 32px;
                }
            }
        }
        .el-checkbox-group{
            padding: 10px;
            display: flex;
            flex-flow: wrap;
            > span{
                display: inline-block;
                margin: 0 10px;
                line-height: 20px;
                min-width: 140px;
                text-align: left;
                &>.el-checkbox .el-checkbox__label{
                    display: inline-block;
                    margin-left: 10px;
                }
            }
        }
    }
    .el-dialog__wrapper{
        .el-dialog{
            margin-top: 0 !important;
        }
        .el-form{
            .el-form-item__label{
                width: 200px !important;
            }
            .el-form-item__content{
                margin-left: 200px !important;
            }
            textarea{
                min-height: 278px !important;
                padding: 10px;
            }
        }
        .el-dialog__footer{
            text-align: center;
        }
    }
}
</style>




