<template>
    <div id="guideSummaryAdd">
        <el-form ref="form">
            <h3 class="form_title">
                <el-form-item label="指南汇总标题：">
                    <el-input v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="归属领域：">
                    <el-select v-model="form.ownershipDomain">
                        <el-option v-for="(item,index) in optGroup1" :key="index" :label="item.content" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="建议立项时间：">
                    <el-date-picker
                        v-model="form.date"
                        type="date"
                        placeholder="选择日期"
                        value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
            </h3>
            <table class="form_inner_table">
                <thead>
                    <tr>
                        <td></td>
                        <td>序号</td>
                        <td>指南建议名称</td>
                        <td>填报单位</td>
                        <td>单位类别</td>
                        <td>所属领域</td>
                        <td>填报类别</td>
                        <td>建议立项时间</td>
                        <td style="padding: 0 97px;">建议理由及依据</td>
                        <td style="padding: 0 65px;">主要研究内容和关键技术</td>
                        <td style="padding: 0 75px;">预期目标和成果</td>
                        <td>拟出标准、技术规范、法规名称</td>
                        <td>经费测算（万元）</td>
                        <td>研究期限</td>
                        <td>示范工程规模</td>
                        <td>示范工程点</td>
                        <td>省内从事该领域的主要研究机构</td>
                        <td>填报联系人</td>
                        <td>联系电话（手机）</td>
                        <td style="padding: 0 111px;">备注</td>
                        <td style="padding: 0 95px;">查重结果</td>
                        <td style="padding: 0 95px;">查重备注</td>
                    </tr>
                </thead>
                <tbody>
                    <tr class="classify_title">
                        <td>
                            <el-checkbox :indeterminate="classifyData.isIndeterminate1" v-model="classifyData.checkAll1" @change="handleCheckAllChange(1)"></el-checkbox>
                        </td>
                        <td colspan="21">
                            <span>一、综合示范类（{{ classifyData.classify1.length }}项）</span>
                            <el-button class="btn_submit" @click="handleAdd(1)">添加</el-button>
                            <el-button class="btn_delete" @click="handleDelete(1)">删除</el-button>
                        </td>
                    </tr>
                    <tr v-for="(item,index) in classifyData.items1" :key="'a'+index" class="classify_content">
                        <td class="text_center">
                            <el-checkbox v-model="classifyData.checkedGroup1" @change="handleCheckedGroupChange(1)" :label="classifyData.classify1[index]"></el-checkbox>
                        </td>
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>
                            <el-date-picker
                                v-model="form.date"
                                type="date"
                                placeholder="选择日期"
                                value-format="yyyy-MM-dd">
                            </el-date-picker>
                        </td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>
                            <el-input 
                                v-model="item.note" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                        <td>
                            <el-input 
                                v-model="item.check_back_result" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                        <td>
                            <el-input 
                                v-model="item.check_back_note" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                    </tr>
                    <tr class="classify_title">
                        <td>
                            <el-checkbox :indeterminate="classifyData.isIndeterminate2" v-model="classifyData.checkAll2" @change="handleCheckAllChange(2)"></el-checkbox>
                        </td>
                        <td colspan="21">
                            <span>二、重大技术公关类（{{ classifyData.classify2.length }}项）</span>
                            <el-button class="btn_submit" @click="handleAdd(2)">添加</el-button>
                            <el-button class="btn_delete" @click="handleDelete(2)">删除</el-button>
                        </td>
                    </tr>
                    <tr v-for="(item,index) in classifyData.items2" :key="'b'+index" class="classify_content">
                        <td class="text_center">
                            <el-checkbox v-model="classifyData.checkedGroup2" @change="handleCheckedGroupChange(2)" :label="classifyData.classify2[index]"></el-checkbox>
                        </td>
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>
                            <el-date-picker
                                v-model="form.date"
                                type="date"
                                placeholder="选择日期"
                                value-format="yyyy-MM-dd">
                            </el-date-picker>
                        </td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>
                            <el-input 
                                v-model="item.note" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                        <td>
                            <el-input 
                                v-model="item.check_back_result" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                        <td>
                            <el-input 
                                v-model="item.check_back_note" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                    </tr>
                    <tr class="classify_title">
                        <td>
                            <el-checkbox :indeterminate="classifyData.isIndeterminate3" v-model="classifyData.checkAll3" @change="handleCheckAllChange(3)"></el-checkbox>
                        </td>
                        <td colspan="21">
                            <span>三、技术研发类（{{ classifyData.classify3.length }}项）</span>
                            <el-button class="btn_submit" @click="handleAdd(3)">添加</el-button>
                            <el-button class="btn_delete" @click="handleDelete(3)">删除</el-button>
                        </td>
                    </tr>
                    <tr v-for="(item,index) in classifyData.items3" :key="'c'+index" class="classify_content">
                        <td class="text_center">
                            <el-checkbox v-model="classifyData.checkedGroup3" @change="handleCheckedGroupChange(3)" :label="classifyData.classify3[index]"></el-checkbox>
                        </td>
                        <td class="text_center">{{ index + 1 }}</td>
                        <td>{{ item.guide_name }}</td>
                        <td>{{ item.fill_unit }}</td>
                        <td>{{ item.unit_category }}</td>
                        <td>{{ item.domain }}</td>
                        <td>{{ item.category }}</td>
                        <td>
                            <el-date-picker
                                v-model="form.date"
                                type="date"
                                placeholder="选择日期"
                                value-format="yyyy-MM-dd">
                            </el-date-picker>
                        </td>
                        <td>{{ item.reason_basis }}</td>
                        <td>{{ item.research_content_technology }}</td>
                        <td>{{ item.expected_target_outcome }}</td>
                        <td>{{ item.standards_specifications_regulatory }}</td>
                        <td>{{ item.research_fund }}</td>
                        <td>{{ item.research_period }}</td>
                        <td>{{ item.demonstration_scale }}</td>
                        <td>{{ item.demonstration_point }}</td>
                        <td>{{ item.province_domain_mechanism }}</td>
                        <td>{{ item.fill_contacts }}</td>
                        <td>{{ item.contact_phone }}</td>
                        <td>
                            <el-input 
                                v-model="item.note" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                        <td>
                            <el-input 
                                v-model="item.check_back_result" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                        <td>
                            <el-input 
                                v-model="item.check_back_note" 
                                :autosize="{ minRows: 1}"
                                type="textarea">
                            </el-input>
                        </td>
                    </tr>
                </tbody>
            </table>
        </el-form>
        <div class="btn_group">
            <el-button class="btn_submit" @click="handleSubmit">提交</el-button>
            <el-button class="btn_back" @click="handleBack">返回</el-button>
        </div>
        <div class="cover_box" :class="{over_box_active:overBoxHide}">
            <div class="message_box">
                <span class="btn_close" @click="handleCloseCover"></span>
                <header class="message_box_header">
                    <h2 class="title">指南添加</h2>
                </header>
                <section class="message_box_content" v-if="refreshFlag">
                    <messageBox @receipt="receiptChildInfo"></messageBox>
                </section>
                <div class="btn_group">
                    <el-button @click="handleConfirmCover">确定</el-button>
                    <el-button @click="handleCancelCover">取消</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import GuideSummaryMessageBox from '@/components/guideManage/guideSummaryMessageBox'
    export default {
        name: 'guideSummaryAdd',
        components: {
            messageBox: GuideSummaryMessageBox
        },
        data() {
            return {
                classifyData: {
                    classify1: [],
                    classify2: [],
                    classify3: [],
                    classifyIndex: 0,
                    checkAll1: false,
                    checkAll2: false,
                    checkAll3: false,
                    isIndeterminate1: false,
                    isIndeterminate2: false,
                    isIndeterminate3: false,
                    checkedGroup1: [],
                    checkedGroup2: [],
                    checkedGroup3: [],
                    items1: [],
                    items2: [],
                    items3: []
                },
                overBoxHide: true,
                refreshFlag: true,
                selectedIDs: [],
                receiptChildData: [],
                form: {
                    title: '',
                    date: '',
                    ownershipDomain: ''
                },
                ids: [],
                optGroup1: []
            }
        },
        methods: {
            handleSubmit() {
                const loading = this.$loading({
                    lock: true,
                    text: '请稍后...',
                    spinner: 'el-icon-loading',
                    background: 'rgba(255,255,255,0.7)'
                });
                // 数据转换，后端传的key有问题···
                let data = [],
                    items1 = this.classifyData.items1,
                    items2 = this.classifyData.items2,
                    items3 = this.classifyData.items3,
                    category = "",
                    domain = "";
                for(let i in items1) {
                    for(let j in this.ids) {
                        if(items1[i].category == this.ids[j].content) {
                            category = this.ids[j].id;
                        }
                    }
                    for(let j in this.ids) {
                        if(items1[i].domain == this.ids[j].content) {
                            domain = this.ids[j].id;
                        }
                    }
                    let item = {
                        "category": category,
                        "checkBackNote": items1[i].check_back_note,
                        "checkBackResult": items1[i].check_back_result,
                        "contactPhone": items1[i].contact_phone,
                        "createTime": "",
                        "demonstrationPoint": items1[i].demonstration_point,
                        "demonstrationScale": items1[i].demonstration_scale,
                        "domain": domain,
                        "expectedTargetOutcome": items1[i].expected_target_outcome,
                        "fillContacts": items1[i].fill_contacts,
                        "fillUnit": items1[i].fill_unit,
                        "guideName": items1[i].guide_name,
                        "guideSummaryTitle": this.form.title,
                        "id": "1",
                        "note": items1[i].note,
                        "ownershipCategory": "76",
                        "projectTime": this.form.date,
                        "provinceDomainMechanism": items1[i].province_domain_mechanism,
                        "reasonBasis": items1[i].reason_basis,
                        "researchContentTechnology": items1[i].research_content_technology,
                        "researchFund": items1[i].research_fund,
                        "researchPeriod": items1[i].research_period,
                        "standardsSpecificationsRegulatory": items1[i].standards_specifications_regulatory,
                        "unitCategory": '暂时数据',
                        "ownershipDomain": this.form.ownershipDomain,
                        "creator": 'xxxx'
                    }
                    data.push(item)
                }
                for(let i in items2) {
                    for(let j in this.ids) {
                        if(items2[i].category == this.ids[j].content) {
                            category = this.ids[j].id;
                        }
                    }
                    for(let j in this.ids) {
                        if(items2[i].domain == this.ids[j].content) {
                            domain = this.ids[j].id;
                        }
                    }
                    let item = {
                        "category": category,
                        "checkBackNote": items2[i].check_back_note,
                        "checkBackResult": items2[i].check_back_result,
                        "contactPhone": items2[i].contact_phone,
                        "createTime": "",
                        "demonstrationPoint": items2[i].demonstration_point,
                        "demonstrationScale": items2[i].demonstration_scale,
                        "domain": domain,
                        "expectedTargetOutcome": items2[i].expected_target_outcome,
                        "fillContacts": items2[i].fill_contacts,
                        "fillUnit": items2[i].fill_unit,
                        "guideName": items2[i].guide_name,
                        "guideSummaryTitle": this.form.title,
                        "id": "1",
                        "note": items2[i].note,
                        "ownershipCategory": "78",
                        "projectTime": this.form.date,
                        "provinceDomainMechanism": items2[i].province_domain_mechanism,
                        "reasonBasis": items2[i].reason_basis,
                        "researchContentTechnology": items2[i].research_content_technology,
                        "researchFund": items2[i].research_fund,
                        "researchPeriod": items2[i].research_period,
                        "standardsSpecificationsRegulatory": items2[i].standards_specifications_regulatory,
                        "unitCategory": '暂时数据',
                        "ownershipDomain": this.form.ownershipDomain,
                        "creator": 'xxxx'
                    }
                    data.push(item)
                }
                for(let i in items3) {
                    for(let j in this.ids) {
                        if(items3[i].category == this.ids[j].content) {
                            category = this.ids[j].id;
                        }
                    }
                    for(let j in this.ids) {
                        if(items3[i].domain == this.ids[j].content) {
                            domain = this.ids[j].id;
                        }
                    }
                    let item = {
                        "category": category,
                        "checkBackNote": items3[i].check_back_note,
                        "checkBackResult": items3[i].check_back_result,
                        "contactPhone": items3[i].contact_phone,
                        "createTime": "",
                        "demonstrationPoint": items3[i].demonstration_point,
                        "demonstrationScale": items3[i].demonstration_scale,
                        "domain": domain,
                        "expectedTargetOutcome": items3[i].expected_target_outcome,
                        "fillContacts": items3[i].fill_contacts,
                        "fillUnit": items3[i].fill_unit,
                        "guideName": items3[i].guide_name,
                        "guideSummaryTitle": this.form.title,
                        "id": "1",
                        "note": items3[i].note,
                        "ownershipCategory": "77",
                        "projectTime": this.form.date,
                        "provinceDomainMechanism": items3[i].province_domain_mechanism,
                        "reasonBasis": items3[i].reason_basis,
                        "researchContentTechnology": items3[i].research_content_technology,
                        "researchFund": items3[i].research_fund,
                        "researchPeriod": items3[i].research_period,
                        "standardsSpecificationsRegulatory": items3[i].standards_specifications_regulatory,
                        "unitCategory": '暂时数据',
                        "ownershipDomain": this.form.ownershipDomain,
                        "creator": 'xxxx'
                    }
                    data.push(item)
                }
                this.axios({
                    url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/batchInsertSummary',
                    method: 'post',
                    data
                }).then((res) => {
                    loading.close();
                    if(res.data.resultFlag == 0) {
                        this.$alert('提交成功','提示', {
                            confirmButtonText: '确定',
                            type: 'success',
                            callback: action => {
                                this.$router.go(-1);
                            }
                        });
                    }else {
                        this.$alert('提交失败','提示', {
                            confirmButtonText: '确定',
                            type: 'warning',
                            callback: action => {}
                        });
                    }
                }).catch((err) => {
                    loading.close();
                    this.$alert('提交失败','提示', {
                        confirmButtonText: '确定',
                        type: 'warning',
                        callback: action => {}
                    });
                })
            },
            handleBack() {
                this.$router.go(-1);
            },
            handleAdd(val) {
                this.overBoxHide = false;
                this.classifyData.classifyIndex = val;
            },
            handleDelete(val) {
                let isIndeterminate = ["isIndeterminate" + val],
                    checkedGroup = ["checkedGroup" + val],
                    checkAll = ["checkAll" + val],
                    classify = ["classify" + val];
                // 要删除的
                let deleteData = this.classifyData[checkedGroup];
                if(deleteData.length == 0) {
                    this.$alert('请选择一条数据', '提示', {
                        confirmButtonText: '确定',
                        type: 'warning',
                        callback: action => {}
                    });
                    return false;
                }
                this.$confirm('确定要删除吗？', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
                    // 现有的
                    let allData = this.classifyData[classify];
                    let indexs = [];
                    for(let i in allData) {
                        for(let j in deleteData) {
                            if(allData[i] == deleteData[j]) {
                                indexs.push(i)
                            }
                        }
                    }
                    // 倒序删除
                    indexs.reverse();
                    for(let i in indexs) {
                        this.classifyData[classify].splice(indexs[i],1);
                    }
                    this.classifyData[checkedGroup] = [];
                    this.classifyData[isIndeterminate] = false;
                    this.classifyData[checkAll] = false;
                }).catch(() => {});
            },
            receiptChildInfo(val,data) {
                this.selectedIDs = val;
                this.receiptChildData = data;
            },
            handleCheckAllChange(val) {
                let isIndeterminate = ["isIndeterminate" + val],
                    checkedGroup = ["checkedGroup" + val],
                    checkAll = ["checkAll" + val],
                    classify = ["classify" + val];
                if(this.classifyData[checkAll]) {
                    this.classifyData[checkedGroup] = this.classifyData[classify];
                }else {
                    this.classifyData[checkedGroup] = [];
                }
                this.classifyData[isIndeterminate] = false;
            },
            handleCheckedGroupChange(val) {
                let isIndeterminate = ["isIndeterminate" + val],
                    checkedGroup = ["checkedGroup" + val],
                    checkAll = ["checkAll" + val],
                    classify = ["classify" + val],
                    checkedGroupLength = this.classifyData[checkedGroup].length,
                    classifyLength = this.classifyData[classify].length;
                if(checkedGroupLength != classifyLength) {
                    this.classifyData[isIndeterminate] = true;
                }else {
                    this.classifyData[isIndeterminate] = false;
                    this.classifyData[checkAll] = true;
                }
                if(checkedGroupLength == 0) {
                    this.classifyData[isIndeterminate] = false;
                    this.classifyData[checkAll] = false;
                }
            },
            handleCloseCover() {
                this.overBoxHide = true;
            },
            handleConfirmCover() {
                let currentClassify = this.classifyData["classify" + this.classifyData.classifyIndex],
                    currentItems = this.classifyData["items" + this.classifyData.classifyIndex];
                if(this.selectedIDs.length == 0) {
                    this.$alert('请选择一条数据','提示', {
                        confirmButtonText: '确定',
                        type: 'warning',
                        callback: action => {}
                    });
                }else {
                    // 判断是否重复
                    for(let i in currentClassify) {
                        for(let j in this.selectedIDs) {
                            if(currentClassify[i] == this.selectedIDs[j]) {
                                this.$alert('请勿重复选择','提示', {
                                    confirmButtonText: '确定',
                                    type: 'warning',
                                    callback: action => {}
                                });
                                return false;
                            }
                        }
                    }
                    // 正常操作
                    this.overBoxHide = true;
                    currentClassify.push(...this.selectedIDs);
                    // 详细数据
                    for(let i in this.receiptChildData) {
                        for(let j in this.selectedIDs) {
                            if(this.receiptChildData[i].id == this.selectedIDs[j]) {
                                currentItems.push(this.receiptChildData[i]);
                            }
                        }
                    }
                    this.selectedIDs = [];
                    // 刷新弹窗
                    this.refreshFlag = false;
                    setTimeout(() => {
						this.refreshFlag = true;
                    },0);
                }
            },
            handleCancelCover() {
                this.overBoxHide = true;
                // 刷新弹窗
                this.refreshFlag = false;
                setTimeout(() => {
                    this.refreshFlag = true;
                },0);
            },
        },
        beforeMount() {
            // 请求所属领域、所属类别
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/getCategoryAndDomain',
                method: 'get',
            }).then((res) => {
                this.ids = res.data.data;
                let data = res.data.data;
                for(let i in data) {
                    if(data[i].classification == "所属领域") {
                        this.optGroup1.push(data[i]);
                    }
                }
            })
        }
        
    }
</script>

<style lang="less">
    #guideSummaryAdd {
        min-height: 100%;
        background-color: #fff;
        overflow-y: hidden;
        padding: 0 20px;
        overflow-x: auto;
        .el-form {
            height: 100%;
            
            .form_title {
                display: flex;
                justify-content: center;
                .el-form-item {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 0 10px;
                    .el-date-editor {
                        width: 197px;
                    }
                }
            }
            .form_inner_table {
                tbody {
                    .classify_title {
                        >:first-child {
                            text-align: center;
                        }
                        td {
                            >span {
                                display: inline-block;
                                width: 215px;
                            }
                        }
                    }
                    .classify_content {
                        .text_center {
                            text-align: center;
                        }
                        td {
                            white-space: nowrap;
                            padding: 0 10px;
                            .el-textarea {
                                padding: 0;
                            }
                        }
                        .el-date-editor {
                            width: 150px;
                        }
                    }
                }
            }
        }
        .btn_group {
            margin: 20px 0;
        }
    }
