<template>
    <div id="guideSummaryList">
        <!-- 搜索 -->
        <div class="queryForm">
            <el-form ref="queryForm" :model="queryForm">
                <el-form-item label="指南汇总标题：" >
                    <el-input v-model="queryForm.guideSummaryTitle"></el-input>
                </el-form-item>
                <el-form-item label="所属领域：">
                    <el-select v-model="queryForm.domain">
                        <el-option v-for="(item,index) in optGroup1" :key="index" :label="item.content" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="所属类别：">
                    <el-select v-model="queryForm.category">
                        <el-option v-for="(item,index) in optGroup2" :key="index" :label="item.content" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="填报单位：">
                    <el-input v-model="queryForm.fillUnit"></el-input>
                </el-form-item>
                <el-form-item label="建议立项时间：">
                    <el-date-picker
                        v-model="queryForm.projectTime"
                        type="date"
                        placeholder="选择日期"
                        value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="主要研究内容和关键技术：">
                    <el-input v-model="queryForm.researchContentTechnology"></el-input>
                </el-form-item>
            </el-form>
            <el-button @click="handleSearch">搜索</el-button>
        </div>

        <!-- 按钮 -->
        <el-button type="primary" size="mini" @click="handleAdd">添加</el-button>

        <!-- 展示列表 -->
        <div class="showList">
            <!-- 表格 -->
            <el-table
                v-loading="loading"
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <!-- <el-table-column
                    type="selection"
                    align="center">
                </el-table-column> -->
                <el-table-column
                    type="index"
                    label="序号"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="guideSummaryTitle"
                    label="指南汇总标题"
                    :show-overflow-tooltip="true"
                    align="center">
                    <template slot-scope="scope">
                        <router-link :to="{
                            name: 'GuideSummaryShow',
                            params: {
                                id: scope.row.guideSummaryTitle
                            }
                        }"> 
                        {{ scope.row.guideSummaryTitle }}
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="ownershipDomain"
                    label="归属领域"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="creator"
                    label="创建人"
                    align="center">
                </el-table-column>
                <el-table-column
                    prop="projectTime"
                    label="建议立项时间"
                    align="center">
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange"
                @handleTableFreshNum="handleTableFresh">
            </pages>
        </div>
    </div>
</template>

<script>
    export default {
        name:'guideSummaryList',
        data() {
            return {
                queryForm: {
                    guideSummaryTitle:'',
                    domain:'',
                    category:'',
                    fillUnit:'',
                    projectTime:'',
                    researchContentTechnology:'',
                    pageNum: 1,
                    pageSize: 10
                },
                optGroup1: [],
                optGroup2: [],
                loading: true,
                tableData: [],
                currentPage: 4,
                fenye: {
                    total: 400, //共有数据多少条
                    pageNum: 1,
                    pageSize: 10, //每页显示的条数
                    pageSizes: [10,20,30,40,50] //选择每页显示多少条
                }
            }
        },
        methods: {
            handleAdd(){
                this.$router.push("/index/guideSummary/guideSummaryAdd");
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
                let ids = [];
                this.multipleSelection.map((item)=> {
                    ids.push(item.id);
                })
                this.selectedIDs = ids;
            },
            handleCurrentChange(val) {              //val表示当前页
                this.queryForm.pageNum = val;
                this._axios();
            },
            handleSizeChange(val) {                 //val表示每页展示的条数
                this.queryForm.pageSize = val;
                this._axios();
            },
            handleTableFresh(){
                this._axios;
                document.querySelector(".first-pager").click();
            },
            // 请求列表数据
            _axios() {
                this.axios({
                    url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/getAllSummary',
                    method: 'get',
                    params: this.queryForm
                }).then((res) => {
                    this.loading = false;
                    let data = res.data.data;
                    if(data == "没有查到相关信息") {
                        this.tableData = []; 
                        this.$alert('没有查到相关信息','提示',{
                            confirmButtonText: '确定',
                            type: 'warning',
                            callback: action => {}
                        });
                    }else {
                        this.tableData = data.list;
                        this.fenye.total = data.total;
                    }
                })
            },
            handleSearch() {
                this.loading = true;
                this.queryForm.pageNum = 1;
                this._axios();
                document.querySelector(".first-pager").click();
            }
        },
        beforeMount() {
            // 请求所属领域、所属类别
            this.axios({
                url: 'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/environment/guide/getCategoryAndDomain',
                method: 'get',
            }).then((res) => {
                let data = res.data.data;
                for(let i in data) {
                    if(data[i].classification == "所属领域") {
                        this.optGroup1.push(data[i]);
                    }else {
                        this.optGroup2.push(data[i]);
                    }
                }
            })
            this._axios();
        }
    }
</script>

<style lang="less">
    #guideSummaryList {
        .queryForm {
            .el-form .el-form-item .el-form-item__label{
                width: 193px;
            }
            .el-date-editor {
                width: 197px;
                .el-input__inner {
                    width: 197px;
                }
            }
        }
        .el-button{
            margin: 10px auto;
        }
        .showList {
            min-height: 640px;
            padding: 10px;
            .el-table{
                min-height: 590px;
            }
            .pages {
                .el-button {
                    background-color: transparent;
                    border: 0;
                    margin: 0;
                    span {
                        line-height: 1;
                    }
                }
            }
        }
    }
</style>



