<template>
    <div id="resultsAddListShow">
        <div class="showForm">
            <el-form :ref="showForm" :model="showForm">
                <table class="form_table">
                    <thead>
                        <tr><th colspan="4">成果新增详情页</th></tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>课题名称：</td>
                            <td>
                                <el-input v-model="showForm.topicName"></el-input>
                            </td>
                            <td>课题编号：</td>
                            <td>
                                <el-input v-model="showForm.topicNumber"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>应用单位名称：</td>
                            <td colspan="3">
                                <el-input v-model="showForm.subjectUndertakingUnit"></el-input>
                            </td>
                            
                        </tr>
                        <tr>
                            <td>应用单位通讯地址（邮编）：</td>
                            <td colspan="3">
                                <el-input v-model="showForm.postalAddress"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>成果应用开始时间：</td>
                            <td>
                                <el-input v-model="showForm.agreementStartTime"></el-input>
                            </td>
                            <td>成果应用结束时间：</td>
                            <td>
                                <el-input v-model="showForm.agreementEndTime"></el-input>
                            </td>
                        </tr>
                        <tr>
                            <td>成果信息附件：</td>
                            <td colspan="3"><el-input v-model="showForm.achievementFileUrl"></el-input></td>
                        </tr>
                    </tbody>
                </table>
            </el-form>
            <el-button type="primary" @click="handleBack">返回</el-button>
        </div>
    </div>
</template>
<script>
export default {
    name:"resultsAddListShow",
    data(){
        return{
            showForm:{
                topicName: "课题名称7",
                topicNumber: "12345",
                postalAddress: "公司地址5",
                subjectUndertakingUnit: "公司名称2",
                agreementStartTime: "2019-07-01",
                agreementEndTime: "2019-07-03",
                achievementFileUrl: "D://xdmd_environment//Extranet//公司名//成果附件//2019-07-18//成果附件3.docx",
                achievementUrlId: "77",
                fileName: "成果附件3.docx",
                id: 54,
                outcomeInformationAllList: null,
            },
            paramsData:{
                id: this.$route.params.id,
                tableData:this.$route.params.arrays
            }
        }
    },
    methods:{
        handleBack(){
            this.$router.back(-1);
        },
        TicketParams(formData) { 
            if(formData != undefined){
                formData.map((item) => {
                    if(this.paramsData.id == item.id){
                        // item.unitNature+=''
                        // item.applicationAcceptanceMode+=''
                        // item.achievementsUrl="1.txt"
                        this.showForm = item
                        return;
                    }
                })
            }
        },
    },
    mounted(){
        this.TicketParams(this.paramsData.tableData)
    }
}
</script>
<style lang="less">
#resultsLibraryShow{
}
</style>



