<template>
    <div id="resultsLibraryList">
        <!-- 搜索 -->
        <div class="queryForm">
            <el-form ref="queryForm" :model="queryForm" >
                <el-form-item label="课题名称：" >
                    <el-input v-model="queryForm.topicName"></el-input>
                </el-form-item>
                <el-form-item label="应用单位名称：">
                    <el-input v-model="queryForm.companyName"></el-input>
                </el-form-item>
            </el-form>
            <el-button type="primary" @click="handleQuery">搜索</el-button>
        </div>

        <!-- 展示列表 -->
        <div class="showList">
            <!-- 表格 -->
            <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                type="selection"
                align="center">
                </el-table-column>
                <el-table-column
                type="index"
                label="序号"
                align="center">
                </el-table-column>
                <el-table-column
                prop="topicName"
                label="课题名称"
                :show-overflow-tooltip="true"
                align="center">
                    <template slot-scope="scope">
                        <router-link :to="{
                                name:'ResultsLibraryShow',
                                params:{
                                    id:scope.row.id,
                                    arrays:tableData
                                }
                            }"> 
                            {{scope.row.topicName}}
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column
                prop="applicationUnitName"
                label="应用单位名称"
                align="center">
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <pages
                :total='fenye.total'
                :currentPage='fenye.pageNum'
                :pageSize='fenye.pageSize'
                :pageSizes="fenye.pageSizes"
                @handleCurrentChangeNum="handleCurrentChange"
                @handleSizeChangeNum="handleSizeChange"
                @handleTableFreshNum="handleTableFresh"
            ></pages>
        </div>
    </div>
</template>
<script>
export default {
    name:'resultsLibraryList',
    data(){
        return{
            queryForm:{
                topicName:'',
                companyName:''
            },
            tableData:[{
                Id:'',
                topicNumber:'',
                topicName:'',
                applicationUnitName:''
            },{
                Id:'',
                topicNumber:'',
                topicName:'',
                applicationUnitName:''
            }],
            multipleSelection: [],
            currentPage:4,
            fenye:{
                total:10, //共有数据多少条
                pageNum:1,
                pageSize:10, //每页显示的条数
                pageSizes:[10,20,30,40] //选择每页显示多少条
            },
        }
    },
    methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
            let ids = []
            this.multipleSelection.map((item)=> {
                ids.push(item.id)
            })
            this.selectedIDs = ids
            console.log('多选',this.selectedIDs)
        },
        // 搜索
        handleQuery(){
            this.getTableData(this.queryForm.topicName,this.queryForm.companyName,this.fenye.pageNum,this.fenye.pageSize)
        },
        // 分页
        handleTableFresh(){
            document.querySelector(".first-pager").click()
            this.getTableData(this.queryForm.topicName,this.queryForm.companyName,this.fenye.pageNum,this.fenye.pageSize)
        },
        handleCurrentChange:function(val){//val表示当前页
            this.fenye.pageNum = val
            this.getTableData(this.queryForm.topicName,this.queryForm.companyName,this.fenye.pageNum,this.fenye.pageSize)
        },
        handleSizeChange(val){//val表示每页展示的条数
            this.fenye.pageSize = val
            this.getTableData(this.queryForm.topicName,this.queryForm.companyName,this.fenye.pageNum,this.fenye.pageSize)
        },
        getTableData(topicName,companyName,Page,total){
            let _this = this
            this.axios({
                method:'POST',
                url:'http://192.168.0.80:8087/Intranet_Environment-1.0-SNAPSHOT/achievement/queryAchievement',
                params:{
                    topicName,
                    companyName,
                    Page,
                    total
                },
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded'
                }
            }).then(function(res){
                if(res.data.data == null){
                    _this.tableData=[]
                    _this.loading = false
                }else{
                    _this.loading = false
                    _this.fenye.total = res.data.data.alltotal
                    _this.tableData = res.data.data.data
                }
                // console.log(_this.tableData)
            }).catch(function(err){
                console.log(err)
            })
        }
    },
    mounted(){
        this.getTableData(this.queryForm.topicName,this.queryForm.companyName,this.fenye.pageNum,this.fenye.pageSize)
    }
}
</script>
<style lang="less">
#resultsLibraryList{
        .showList{
            margin-top: 40px;
        }
        .el-table{
            min-height: 630px;
            padding-bottom: 10px;
            .el-button{
                margin: 0 auto;
                color: #fff;
            }
        }
        .pages{
            .el-button{
                background-color: transparent;
                border: 0;
                margin: 0;
                span{
                    line-height: 1;
                }
            }
        }
    }
</style>



